<template>
          <MainLayout>
               <div class="subject-table-page" style="">
               <br>
               <h1></h1>
               <select v-model="select_year" style="font-size: 20px;">
                    <option value="" disabled></option>
                    <option v-for="y in year" :key="y" :value="y">{{ y }}</option>
               </select>
               <div v-if="student_data.length" style="margin-bottom: 200px;">
                    <h2></h2>
                    <table class="data-stdplan-table">
                    <thead>
                    <tr>
                         <th style="width:120px"></th>
                         <th></th>
                         <th></th>
                         <th></th>
                         <th></th>
                         <th></th>
                    </tr>
                    </thead>

                   
                    <template v-for="g in group_list" :key="'g'+g.group_id">
                    <tbody v-if="tableRows.some(r => r.group_id === g.group_id)">
                         <tr>
                         <td colspan="7" style="font-weight:bold; text-align:left">
                              {{ g.group_id }} {{ g.group_name }}
                         </td>
                         </tr>
                         <tr
                         v-for="row in tableRows.filter(r => r.group_id === g.group_id)"
                         :key="row.subject_id + '-' + row.term"
                         >
                         <td v-if="row.showHead" :rowspan="row.rowspan" style="vertical-align: top;">{{ row.subject_id }}</td>
                         <td v-if="row.showHead" :rowspan="row.rowspan" style="vertical-align:top; text-align:left;">
                              {{ row.subject_name }}
                         </td>
                         <td v-if="row.showHead" :rowspan="row.rowspan" style="vertical-align: top;">
                              {{ row.practical_credits + row.theory_credits }}
                         </td>
                         <td>{{ row.year === 3 ? select_year : Number(select_year)+1 }}</td>
                         <td>{{ row.term }}</td>
                         <td>{{ row.total_student }}</td>
                         </tr>
                    </tbody>
                    </template>
                    </table>
                    <button class="btn-back" style="font-size: 16px; background-color: #00cc00; width: 200px;" @click="openPreviewWindow">
                         <i class="fa-solid fa-file-pdf"></i> 
                    </button>
               </div>
               <div v-if="!student_data.length">
                    <h2></h2>
               </div>
               </div>
          </MainLayout>
     </template>
     
     <script>
     import MainLayout from './main_layout.vue';
     import axios from "axios";
     
     
     export default {
          name: 'tc_summery_plan',
          components: {
               MainLayout
          },
          data() {
               return {
                    year: [],
                    select_year:"",
                    students: [], // 
                    student_data: [], // 
                    structure: [], // 
                    student_plan: [], // 
                    open_subject: [], // 
                    group_list: [], // 
                    std_open_subject: [], //  student_plan  open_subject
               };
          },
          computed: {
               tableRows () {
                    // 1)  group_id
                    const gmap = Object.fromEntries(this.group_list.map(g => [g.group_id, g.group_name]))
                    const rows = []

                    // 2)  rowspan 
                    const byGroup = this.std_open_subject
                         .slice()
                         .sort((a, b) =>
                         a.group_id - b.group_id ||
                         a.subject_id.localeCompare(b.subject_id) ||
                         a.term - b.term
                         )

                    // 3)  rowspan
                    let i = 0
                    while (i < byGroup.length) {
                         const curr = byGroup[i]
                         const sameSubj = byGroup.filter(r =>
                         r.group_id === curr.group_id && r.subject_id === curr.subject_id
                         )
                         const span = sameSubj.length   //  = rowspan

                         sameSubj.forEach((r, idx) => {
                         rows.push({
                              ...r,
                              group_name: gmap[r.group_id] || '',
                              showHead: idx === 0,   //  subject
                              rowspan:   idx === 0 ? span : 0
                         })
                         })

                         i += span
                    }
                    return rows
               },
          },

          methods: {
               async fetchStudent_list() {
                    try {
                    const response = await axios.get("http://127.0.0.1:3000/api/getStudent_plan"); // URL  API
                    if (response.data.result === "1") {
                         console.log(response.data.datalist);
                         this.students = response.data.datalist; //  state
                         this.extract_year();
                    } 
                    }
                    catch(error) {
                         console.error("Error fetching student data:", error);
                    }
               },
               extract_year(){
                    const allYears = this.students.map(s => s.batch_year); //  year  student
                    this.year = [...new Set(allYears)].sort((a, b) => b - a); //  Set
               },
               extract_students_by_year() {
                    const student = this.students.filter(student =>
                         student.batch_year === this.select_year && student.status === 1
                    );
                    this.student_data = student
                    this.fetchStudent_plan();
                    
               },
               async fetchStudent_plan(){
               const student_id = this.student_data.map(s => s.std_id); //  id 
               this.student_plan = []; // 
               for(let i = 0; i < student_id.length; i++){
                    try{
                         const response = await axios.post("http://127.0.0.1:3000/api/getStudentplan_list", {
                              std_id: student_id[i] //  id 
                         });
                         if (response.data.result === "1") {
                              this.student_plan.push(...response.data.datalist);
                              
                         } else if(response.data.result === "0"){
                              this.student_plan = [];
                         }  
                    }
                    catch (error) {
                         console.error("Error fetching subjects:", error);
                    }
               }
               this.gen_student_subject(); //  subject
          },
          async fetchStructure(){ //
               try {
                    const response = await axios.get("http://127.0.0.1:3000/api/getStructure",{
                    }); // URL  API
                    this.group_list = response.data.datalist;
               } catch (error) {
                    console.error("Error fetching subject:", error);
               }
          },
          async fetchOpen_subject(){
               console.log(this.selectGroup)
               try{
                    const response = await axios.post("http://127.0.0.1:3000/api/getopensubject",{
                              
                         }); 
                         if(response.data.result == "1"){
                              this.open_subject = response.data.datalist;
                              this.groupPlans();
                              
                         }
                         else if(response.data.result == "2"){
                              this.open_subject = [];
                              this.groupPlans();
                              
                         }
               }
               catch(error){
                    console.error("Error fetching subject:", error);
               }
          },
          gen_student_subject() {
               this.std_open_subject = [];
               /* 1) map  open_subject (subject_id|term) */
               const openMap = {};
               this.open_subject.forEach(os => {
                    const key = `${os.subject_id}|${os.term}`;
                    if (!openMap[key]) openMap[key] = os;   // 
               });

               /* 2)  ( study_year  student_plan) */
               const counter = {}; // subject_id|term|year
               this.student_plan.forEach(sp => {
                    const openKey = `${sp.subject_id}|${sp.term}`;
                    if (!openMap[openKey]) return;          //   
                    const key = `${sp.subject_id}|${sp.term}|${sp.year}`;
                    counter[key] = (counter[key] || 0) + 1;
               });

               /* 3)  rows */
               let no = 1;
               this.std_open_subject = Object.keys(counter).map(k => {
                    const [subject_id, termStr, yearStr] = k.split('|');
                    const open = openMap[`${subject_id}|${termStr}`];
                    return {
                         no: no++,
                         subject_id,
                         subject_name:      open.subject_name,
                         theory_credits:    open.theory_credits,
                         practical_credits: open.practical_credits,
                         year:  Number(yearStr),          //  ()
                         term:  Number(termStr),
                         total_student: counter[k],
                         group_id: open.group_id,
                    };
               });

               /*  subject_id, year, term */
               this.std_open_subject.sort((a, b) =>
                    a.subject_id.localeCompare(b.subject_id) ||
                    a.year - b.year ||
                    a.term - b.term
               );
          },
                    openPreviewWindow(){
                    const title = encodeURIComponent("");
                    const students_plan = encodeURIComponent(JSON.stringify(this.std_open_subject));
                    const group = encodeURIComponent(JSON.stringify(this.group_list));


                    const pdfUrl = `http://127.0.0.1:3000/api/pdf_summery_plan?title=${title}&students_plan=${students_plan}&group=${group}&year=${this.select_year}`;

                    //  PDF  
                    window.open(pdfUrl, "_blank");
                         
               },
          
          },
          watch: {
          select_year(newYear) {
               console.log("Selected year:", newYear);
               // 
               this.extract_students_by_year();
          },
          },
          mounted() {
               this.fetchStudent_list();
               this.fetchStructure(); // 
               this.fetchOpen_subject(); // 
          },
     };
     </script>