<!-- Home.vue -->
<template>
<MainLayout>
<div class="main-content" >
     <template v-if="user.user_status === '3'">

     <div class="logo">
     <img src="@/assets/RMUTI_KORAT.png" alt="Logo" style="height: 200px;">
     </div>
     <div class="table-headers" style="  display: flex;
                                        justify-content: space-between;
                                        width: 40%;
                                        text-align: left;
                                        font-weight: bold;
                                        margin-bottom: 10px;
                                        margin-left: 20px;">
     <h2></h2>
     <h2></h2>
     </div>
     <div class="tables-container" 
     style=" display: flex; /*  flexbox  */
               justify-content: space-between; /*  */
               gap: 20px; /*  */">
     
     <div class="table-container" style="width: 50%;">
     <ul class="space-y-2">
          <li v-for="(groups, category) in structure_group" :key="category">
          <div @click="toggleCategory(category)" class="cursor-pointer font-bold flex justify-between items-center p-2 rounded-md hover:bg-gray-200 transition duration-200 w-full">
               <span>{{ category }}</span>
               <!--  Font Awesome  chevron-right   chevron-down  -->
               <i :class="expandedCategories[category] ? 'fa-solid fa-chevron-down rotate-90' : 'fa-solid fa-chevron-right'" class="transition-transform"></i>
          </div>
          <ul v-if="expandedCategories[category]" class="ml-6 mt-1 space-y-1 transition-all">
               <li v-for="group in groups" :key="group.group_id" class="text-gray-700 pl-2 border-l border-gray-300">
               <div @click="fetchSubject(group.group_id)" class="cursor-pointer flex justify-between items-center p-2 rounded-md hover:bg-gray-200 transition duration-200 w-full">
               <span>{{ group.group_id + ". " + group.group_name }}</span>
               <!--  Font Awesome  chevron-right  chevron-down  -->
               </div>
               </li>
          </ul>
          </li>
     </ul>
     <p v-if="subject_list.length == 0"></p>
          <div v-if="subject_list.length > 0">
               <table class="data-table">
               <thead>
                    <tr>
                    <th style="width: 140px;"></th>
                    <th style="width: 400px"></th>
                    <!-- <th style="width: 100px"></th> -->
                    </tr>
               </thead>
               <tbody>
                    <tr v-for="subject in subject_list" :key="subject.subject_id">
                         <td>{{ subject.subject_id }}</td>
                         <td>{{ subject.subject_nameTh }}</td>
                    </tr>
               </tbody>
               </table>
          </div>
     </div>
          <div >
          <table class="data-table">
               <thead>
                    <tr>
                         <th></th>
                         <th></th>
                         <th></th>
                         <th></th>
                         <th></th>
                    </tr>
               </thead>
               <tbody>
                    <tr v-for="item in adjust_list" :key="item.std_id">
                         <td >{{ item.date_add }}</td>
                         <td >{{ item.date_updated || "-" }}</td>
                         <td >{{ item.user_ap || "-" }}</td>
                         <td v-if="item.status == 1" style="color: green;">{{ convertStatusToText(item.status) }}</td>
                         <td v-if="item.status == 2" style="color: red;">{{ convertStatusToText(item.status) }}</td>
                         <td v-if="item.status == 0" style="color: blue;">{{ convertStatusToText(item.status) }}</td>
                         <td>{{ item.reason || "-" }}</td>
                    </tr>
               </tbody>
          </table>
     </div>
     </div>
     </template>
     <template v-else>
     <div class="welcome-text">
     <h1></h1>
     <p>
          
     </p>
     </div>
     <div class="logo">
     <img src="@/assets/RMUTI_KORAT.png" alt="Logo">
     </div>
     </template>
</div>
</MainLayout>
</template>

<script>
     import axios from "axios";
     import { inject, computed, ref } from "vue";
     import MainLayout from './main_layout.vue';

     export default {
     setup() {
          const user = inject("user"); //  user  MainLayout.vue
          const userData = computed(() => user?.value || {}); //  undefined
          const expandedCategories = ref({}); //  category
          const expandedGroups = ref({}); //  group_id

          const toggleCategory = (category) => {
               expandedCategories.value[category] = !expandedCategories.value[category];
          };

          const toggleGroup = (groupId) => {
               expandedGroups.value[groupId] = !expandedGroups.value[groupId];
          };

          console.log("User Data:", userData.value); // 

          return { user: userData,
                    expandedCategories,
                    expandedGroups,
                    toggleCategory,
                    toggleGroup,
     };
     },
     name: 'homePage',
     components: {
     MainLayout  
     },
     data() {
     return {
          group_list: [],
          subject_list: [],
          structure_group_subject: [],
          adjust_list: [],
     };
     },
     computed: {
     structure_group() {
          const groupedBySort = this.group_list.reduce((acc, group) => {
          if (!acc[group.sort]) {
          acc[group.sort] = [];
          }
          acc[group.sort].push(group);
          return acc;
          }, {});

          const groupedSubjects = this.subject_list.reduce((acc, subject) => {
          if (!acc[subject.group_id]) {
          acc[subject.group_id] = [];
          }
          acc[subject.group_id].push(subject);
          return acc;
          }, {});

          Object.values(groupedBySort).forEach((groupArray) => {
          groupArray.forEach((group) => {
          group.subjects = groupedSubjects[group.group_id] || [];
          });
          });

          return groupedBySort;
     },

     },
     methods: {
     //  API
     async fetchStructure(){
          try {
          const response = await axios.get("http://127.0.0.1:3000/api/getStructure",{

          }); // URL  API
          this.group_list = response.data.datalist; //  state
          console.log(response.data); //  API
          
          } catch (error) {
          console.error("Error fetching subject:", error);
          }
     },
     async fetchSubject(group_id) {
          try {
          const response = await axios.post("http://127.0.0.1:3000/api/getSubject",{
               group_id: group_id,
          }); // URL  API
          console.log(response.data); //  API
          if(response.data.result == "1"){
               this.subject_list = response.data.datalist; //  state
               
          }
          else{
               this.subject_list = []; //  state
          }
          } catch (error) {
          console.error("Error fetching subject:", error);
          }
     },
     async fetchAdjust_list(){
          try{
               const response = await axios.post("http://127.0.0.1:3000/api/getAdjust_list",{
               std_id: this.user.std_id,
               }); // URL  API
               if (response.data.result === "1") {
                    console.log(response.data.datalist);
                    this.adjust_list = response.data.datalist; //  state
                    const sort_List = this.adjust_list.sort((a, b) => {
                         const dateA = this.convert_date(a.date_add); //  ..  ..
                         const dateB = this.convert_date(b.date_add);
                         return dateB - dateA; // 
                    });
                    this.adjust_list = sort_List; //  state
                    
               } 
          }
          catch (error) {
               console.error("Error fetching student data:", error);
          }
     },
     convert_date(thaiDateStr) { //  ..  ..  JS
          const [day, month, year] = thaiDateStr.split('-').map(Number);
          const christianYear = year - 543; //  ..  ..
          return new Date(christianYear, month - 1, day); //  JS  0
     },

     convertStatusToText(status) {
          const statusMapping = {
               1: "",
               2: "",
               0: "",
          };
          return statusMapping[status] || "";
     },
     },
     async mounted() {
          await this.fetchSubject();
          await this.fetchAdjust_list();
          await this.fetchStructure();
     },
     };
</script>
<!--  CSS  -->
<style>
@import "@/assets/css/styles.css";
</style>
