<template>
     <MainLayout>
       <div class="question-form">
         <h2></h2>
   
         <!--  -->
         <div class="form-group">
           <label for="question">:</label>
           <input 
             v-model="question" 
             id="question" 
             type="text" 
             placeholder="" 
             class="form-control"
             :class="{ 'is-invalid': questionError }"
           />
           <div v-if="questionError" class="invalid-feedback">{{ questionError }}</div>
         </div>
         <button @click="addQuestion" class="btn btn-primary"></button>
         <br> <br>
         <!--  -->
         <div class="form-group">
           <label for="answer">:</label>
           <input 
             v-model="answer" 
             id="answer" 
             type="text" 
             placeholder="" 
             class="form-control"
             :class="{ 'is-invalid': answerError }"
           />
           <div v-if="answerError" class="invalid-feedback">{{ answerError }}</div>
         </div>

         <button @click="addAnswer" class="btn btn-primary"></button>
       </div>
     </MainLayout>
   </template>
   
   <script>
   import MainLayout from './main_layout.vue';
   import axios from "axios";
   
   export default {
     name: 'tc_adddata_bot',
     components: {
       MainLayout  
     },
     data() {
       return {
         // 
         question: '', // 
         answer: '',   // 
         questionError: '', // 
         answerError: ''    // 
       };
     },
     methods: {
       //  API
       async addQuestion() {
         // 
         if (!this.question) {
           this.questionError = "";
           return;
         } else {
           this.questionError = ''; // 
         }
   
         try {
           const response = await axios.post('http://127.0.0.1:3000/api/setQuestion', {
             question: this.question
           });
           console.log(response.data); //  API
           if (response.data.result === "1") {
             alert("");
             this.question = ''; // 
           } else {
             this.questionError = response.data.message || "";
           }
         } catch (error) {
           this.questionError = error.response?.data?.message || "";
         }
       },
   
       //  API
       async addAnswer() {
         // 
         if (!this.answer) {
            this.answerError = "";
            return;
         }  else {
            this.answerError = ''; // 
         }
   
         try {
           const response = await axios.post('http://127.0.0.1:3000/api/setAnswer', {
             answer: this.answer
           });
           console.log(response.data); //  API
           if (response.data.result === "1") {
             alert("");
             this.answer = ''; // 
           } else {
             this.answerError = response.data.message || "";
           }
         } catch (error) {
           this.answerError = error.response?.data?.message || "";
         }
       }
     }
   };
   </script>
   <style>
   @import "@/assets/css/styles.css";
   </style>