<template>
     <MainLayout>
          <div class="form-container">
               <h2></h2>
               <h3>: {{ std_id }} : {{ std_name }} {{ std_last }}</h3>
          </div>
               <!-- Popup  -->
               <div v-if="showRejectPopup" class="popup-overlay">
                    <div class="popup-content">
                    <h3></h3>
                    <textarea v-model="rejectReason" rows="4" cols="50" style="font-size: 18px;"></textarea>
                    <br>
                    <button class="btn-remove" style="font-size: 18px; margin-right: 10px; background-color: #02bd2a;" @click="rejectPlan"></button>
                    <button class="btn-remove" style="font-size: 18px; background-color: #a3a3a3;" @click="closeRejectPopup"></button>
                    </div>
               </div>

<div v-for="(group, index) in groupedOriData" :key="'group-' + group.yearTerm">
     <!--  -->
     <div class="table-headers" v-if="group.year  === 3 &&  group.term  === 1">
          <h3></h3>
          <h3></h3>
     </div>
     <h3> {{ group.term }}  {{ group.year === 4 ? Number(year) +1 : year }}</h3>
     <div class="tables-wrapper">

          <div class="tables-container">
               <div>
                    <!--  -->
                    <table class="data-table">
                         <thead>
                              <tr>
                                   <th></th>
                                   <th></th>
                                   <th></th>
                                   <th></th>
                              </tr>
                         </thead>
                         <tbody>
                              <tr v-for="item in group.plans" :key="'ori-' + item.subject_id">
                                   <td :style="checkHasChanges(item, 'ori') ? { backgroundColor: '#cccccc'} : {}">{{ item.subject_id }}</td>
                                   <td :style="checkHasChanges(item, 'ori') ? { backgroundColor: '#cccccc', width:'400px', hight:'20px'} : {width:'400px'}">{{ item.subject_name }}</td>
                                   <td :style="checkHasChanges(item, 'ori') ? { backgroundColor: '#cccccc', textAlign:'center'} : {textAlign:'center'}">{{ item.group_id || '-' }}</td>
                                   <td :style="checkHasChanges(item, 'ori') ? { backgroundColor: '#cccccc'} : {}" v-if="item.theory_credits+(item.practical_credits) < 3">
                                   {{ item.practical_credits+item.theory_credits }}({{ item.theory_credits }}-{{ item.practical_credits*2 }}-{{ 3-(item.theory_credits+(item.practical_credits*2)) }})
                                   </td>
                                   <td :style="checkHasChanges(item, 'ori') ? { backgroundColor: '#cccccc'} : {}" v-if=" item.theory_credits+(item.practical_credits) >= 3 && item.theory_credits+(item.practical_credits) <= 5 ">
                                   {{ item.practical_credits+item.theory_credits }}({{ item.theory_credits }}-{{ item.practical_credits*2 }}-{{ 9-(item.theory_credits+(item.practical_credits*2)) }})
                                   </td>
                                   <td :style="checkHasChanges(item, 'ori') ? { backgroundColor: '#cccccc'} : {}" v-if="item.theory_credits+(item.practical_credits) == 6">
                                   {{ item.practical_credits+item.theory_credits }}({{ item.theory_credits }}-{{ item.practical_credits }}-0)
                                   </td>
                              </tr>
                              <tr>
                                   <td colspan="3" style="text-align: right;">:</td>
                                   <td style="text-align: center;">{{ totalCreditsOriData[index]?.totalCredits }}</td>
                              </tr>
                         </tbody>
                    </table>
               </div>
               <div>
                    <!--  -->
                    <table class="data-table">
                         <thead>
                              <tr>
                                   <th></th>
                                   <th></th>
                                   <th></th>
                                   <th></th>
                              </tr>
                         </thead>
                         <tbody>
                              <tr v-for="item in groupedChangeData[index]?.plans || []" :key="'change-' + item.subject_id">
                                   <td :class="{ changed: item.hasChanges }">{{ item.subject_id }}</td>
                                   <td :class="{ changed: item.hasChanges }" :style="{width:'400px',hight:'20px'}">{{ item.subject_name }}</td>
                                   <td :class="{ changed: item.hasChanges }" :style="{textAlign:'center'}">{{ item.group_id || '-' }}</td>
                                   <td :class="{ changed: item.hasChanges }" v-if="item.theory_credits+(item.practical_credits) < 3">
                                   {{ item.practical_credits+item.theory_credits }}({{ item.theory_credits }}-{{ item.practical_credits*2 }}-{{ 3-(item.theory_credits+(item.practical_credits*2)) }})
                                   </td>
                                   <td :class="{ changed: item.hasChanges }" v-if=" item.theory_credits+(item.practical_credits) >= 3 && item.theory_credits+(item.practical_credits) <= 5 ">
                                   {{ item.practical_credits+item.theory_credits }}({{ item.theory_credits }}-{{ item.practical_credits*2 }}-{{ 9-(item.theory_credits+(item.practical_credits*2)) }})
                                   </td>
                                   <td :class="{ changed: item.hasChanges }" v-if="item.theory_credits+(item.practical_credits) == 6">
                                   {{ item.practical_credits+item.theory_credits }}({{ item.theory_credits }}-{{ item.practical_credits }}-0)
                                   </td>
                              </tr>
                              <tr>
                                        <td colspan="3" style="text-align: right;">:</td>
                                        <td style="text-align: center;">{{ totalCreditsChangeData[index]?.totalCredits }}</td>
                              </tr>
                         </tbody>
                    </table>
               </div>
          </div>
     </div>
</div>
<button @click="goBack" class="btn btn-back">
     <i class="fa-solid fa-arrow-left-long"></i> </button>
<div class="btn-group"
               style="   margin-bottom: 100px;
                         display: flex;
                         justify-content: center;
                         gap: 500px;">
               <div>
               <!--  Popup -->
               <button class="btn-remove" style="font-size: 18px; width: 150px;" @click="openRejectPopup">
                    <i class="fa-solid fa-xmark"></i> 
               </button>


               </div>
               <button class="btn-add" style="font-size: 18px; background-color: #02bd2a; margin-left: 20px; width: 150px;" @click="submitPlan">
                    <i class="fa-solid fa-check"></i> 
               </button>
          </div>
     </MainLayout>
</template>

<script>
import MainLayout from './main_layout.vue';
import axios from "axios";
import { inject, computed} from "vue";

export default {
     setup() {
          const user = inject("user"); //  user  MainLayout.vue
          const userData = computed(() => user?.value || {}); //  undefined
          console.log("User Data:", userData.value); // 
          return { user: userData };
     },
     name: 'tc_inspect_adjust',
     components: {
          MainLayout
     },
     data() {
          return {
               std_id: this.$route.query.std_id,
               std_name: this.$route.query.std_name,
               std_last: this.$route.query.std_last,
               year: this.$route.query.year,
               adjust_id: this.$route.query.adjust_id,
               ori_data: [],
               change_data: [],
               temp_change_data: [],
               showRejectPopup: false,
               rejectReason: "",
          };
     },
     computed: {
     groupedOriData() {
          return this.groupPlans(this.ori_data);
     },
     groupedChangeData() {
          return this.groupPlans(this.temp_change_data);
     },
     totalCreditsOriData() {
          return this.calculateTotalCredits(this.groupedOriData);
     },
     totalCreditsChangeData() {
          return this.calculateTotalCredits(this.groupedChangeData);
     },
     },

     methods: {
          async fetchStudent_plan() {
               try {
                    const response = await axios.post("http://127.0.0.1:3000/api/getStudentplan_list", {
                         std_id: this.std_id
                    });
                    if (response.data.result === "1") {
                         this.ori_data = response.data.datalist;
                    } 
               } catch (error) {
                    console.error("Error fetching subjects:", error);
               }
          },
          async fetchChange_plan() {
               try {
                    const response = await axios.post("http://127.0.0.1:3000/api/getAdjust_plan", {
                         adjust_id: this.adjust_id
                    });

                    if (response.data.result === "1") {
                         this.change_data = response.data.datalist;
                         let existingPlans = this.mergeDataWithChanges(this.ori_data, this.change_data);
                         this.temp_change_data = existingPlans;

                         // console.log(this.change_data);
                    }
               } catch (error) {
                    console.error("Error fetching change plan:", error);
               }
          },

          mergeDataWithChanges(oriData, changeData) {
               //  (oriData)
               let mergedData = [...oriData];

               //  changeData  subject_id  oriData 
               changeData.forEach(changeItem => {
                    //  subject_id  oriData 
                    const indexInOri = mergedData.findIndex(oriItem => 
                         oriItem.subject_id === changeItem.subject_id 
                    );

                    // ,  changeData  oriData
                    if (indexInOri !== -1) {
                         mergedData[indexInOri] = { ...changeItem, hasChanges: true };
                    } else {
                         // ,  changeData  mergedData
                         mergedData.push({ ...changeItem, hasChanges: true });
                    }
               });

               return mergedData;
          },
          checkHasChanges(item) {
                    //  change_data
                    const match = this.temp_change_data.find(
                         data => 
                              data.subject_id === item.subject_id && 
                              data.term === item.term && 
                              data.year === item.year
                    );

                    //   change_data 
                    return !match || JSON.stringify(match) !== JSON.stringify(item);
          },
          groupPlans(dataSource) {
          // - ( ori_data, change_data  merged_data)
          const yearTermPairs = new Set(dataSource.map(item => `${item.year}-${item.term}`));

          return Array.from(yearTermPairs)
               .map(pair => pair.split("-").map(Number))
               .sort((a, b) => a[0] - b[0] || a[1] - b[1]) // 
               .map(([year, term]) => {
                    const plans = dataSource
                         .filter(plan => plan.year === year && plan.term === term)
                         .sort((a, b) => a.group_id.localeCompare(b.group_id)); // 

                    return {
                         yearTerm: `${year}-${term}`,
                         year,
                         term: term === 3 ? "" : term,
                         plans,
                    };
               });
          },
          calculateTotalCredits(groupedData) {
               return groupedData.map(group => {
                    const totalCredits = group.plans.reduce((sum, plan) => {
                         const theoryCredits = Number(plan.theory_credits) || 0;
                         const practicalCredits = Number(plan.practical_credits) || 0;
                         return sum + (theoryCredits + practicalCredits);
                    }, 0);

                    return {
                         ...group,
                         totalCredits
                    };
               });
          },
          async submitPlan(){
               if(confirm("")){
                    try {
                         const response = await axios.post("http://127.0.0.1:3000/api/updateAdjust_plan", {
                              std_id: this.std_id,
                              adjust_id: this.adjust_id,
                              plans: this.change_data,
                              user: this.user.user_name,
                              status: 1
                         });
                         if (response.data.result === "1") {
                              this.change_data = response.data.datalist;
                              this.$router.go(-1);
                         } else {
                              // alert("");
                         }
                    
                    } catch (error) {
                         console.error("Error fetching change plan:", error);
                    }
               }
          },
          //  Popup
          openRejectPopup() {
               this.showRejectPopup = true;
          },
          //  Popup
          closeRejectPopup() {
               this.showRejectPopup = false;
          },
          async rejectPlan(){
               if(confirm("")){
                    try {
                         const response = await axios.post("http://127.0.0.1:3000/api/updateAdjust_plan", {
                              std_id: this.std_id,
                              adjust_id: this.adjust_id,
                              plans: this.change_data,
                              user: this.user.user_name,
                              reason: this.rejectReason,
                              status: 2
                         });
                         if (response.data.result === "1") {
                              this.change_data = response.data.datalist;
                              this.$router.go(-1);
                         } else {
                              // alert("");
                         }
                    } catch (error) {
                         console.error("Error fetching change plan:", error);
                    }
               }
          },
          goBack() {
               this.$router.go(-1);
          }
     },
     mounted() {
          this.fetchStudent_plan().then(() => {
          this.fetchChange_plan();
    });
}

};
</script>