<template>
     <MainLayout>
          <div class="subject-table-page">
               <br>
               <h1></h1>
               <select v-model="select_year" style="font-size: 20px;">
                    <option value="" disabled></option>
                    <option v-for="y in year" :key="y" :value="y">{{ y }}</option>    
               </select>
               <div v-if="student_data.length">
                    <h2> {{ select_year }}</h2>
                    <table class="data-stdplan-table">
                         <thead>
                              <tr>
                                   <th></th>
                                   <th>-</th>
                                   <th></th>
                                   <th></th>
                                   <th></th>
                              </tr>
                         </thead>
                         <tbody>
                              <tr v-for="student in student_data" :key="student.std_id">
                                   <td>{{ student.std_id }}</td>
                                   <td style="text-align: left;">{{ student.std_name }} {{ student.std_last }}</td>
                                   <td>125</td>
                                   <td>{{ student.total_Tcredits }}</td>
                                   <td>{{ student.total_Pcredits }}</td>
                              </tr>
                         </tbody>
                    </table>
                    <button class="btn-back" style="font-size: 16px; background-color: #00cc00; width: 200px;" @click="openPreviewWindow">
                         <i class="fa-solid fa-file-pdf"></i> 
                    </button>
               </div>
               <div v-if="!student_data.length">
                    <h2></h2>
               </div>
          </div>
     </MainLayout>
</template>

<script>
import MainLayout from './main_layout.vue';
import axios from "axios";


export default {
     name: 'tc_summery_tranfer',
     components: {
          MainLayout
     },
     data() {
          return {
               year: [],
               select_year:"",
               students: [], 
               student_data: [], // 
               tranfer_list: [], // 
               student_plan: [], // 
          };
     },
     computed: {
          filteredStudents() {
               return this.students.filter(student => 
                    student.batch_year === this.select_year && student.status === 1
               );
          },
     },
     methods: {
          async fetchStudent_list() {
               try {
               const response = await axios.get("http://127.0.0.1:3000/api/getStudent_plan"); // URL  API
               if (response.data.result === "1") {
                    console.log(response.data.datalist);
                    this.students = response.data.datalist; 
                    this.extract_year(); 
               } 
               }
               catch(error) {
                    console.error("Error fetching student data:", error);
               }
          },

          extract_year(){
               const allYears = this.students.map(s => s.batch_year); //  year  student
               this.year = [...new Set(allYears)].sort((a, b) => b - a); //  Set
          },
          extract_students_by_year() {
               const student = this.students.filter(student =>
                    student.batch_year === this.select_year && student.status === 1
               );
               this.student_data = student
               this.fetchTransfer_list();
               this.fetchStudent_plan();
          },
          async fetchTransfer_list(){ // 
               const student_id = this.student_data.map(s => s.std_id); //  id 
               this.tranfer_list = []; // 
               for(let i = 0; i < student_id.length; i++){
                    try{
                         const response = await axios.post("http://127.0.0.1:3000/api/getTranfer_plan",{
                              std_id: student_id[i] //  id 
                         }); // URL  API
                         if(response.data.result == "1"){
                              const tranfer = response.data.datalist;
                              const total_Tcredits = tranfer.reduce((sum, item) => sum + (item.theory_credits + item.practical_credits), 0); // 
                              const std = this.student_data.find(s => s.std_id === student_id[i]); //  student_data
                              if(std) {
                                   std.total_Tcredits = total_Tcredits; //  student_data
                              }
                              // this.transfer_list[student_id[i]] = response.data.datalist; //  transfer_list
                         }
                         else if(response.data.result == "0"){
                              // this.transfer_list = [];
                         }
                    }
                    catch(error){
                         console.error("Error fetching subject:", error);  
                    }
               }
          },
          async fetchStudent_plan(){
               const student_id = this.student_data.map(s => s.std_id); //  id 
               this.student_plan = []; // 
               for(let i = 0; i < student_id.length; i++){
                    try{
                         const response = await axios.post("http://127.0.0.1:3000/api/getStudentplan_list", {
                              std_id: student_id[i] //  id 
                         });
                         if (response.data.result === "1") {
                              const plan = response.data.datalist;
                              const total_Pcredits = plan.reduce((sum, item) => sum + (item.theory_credits + item.practical_credits), 0); // 
                              const std = this.student_data.find(s => s.std_id === student_id[i]); //  student_data
                              if(std) {
                                   std.total_Pcredits = total_Pcredits; //  student_data
                              }
                              // this.student_plan[student_id[i]] = response.data.datalist; //  student_plan
                         } else if(response.data.result === "0"){
                              this.student_plan = [];
                         }  
                    }
                    catch (error) {
                         console.error("Error fetching subjects:", error);
                    }
               }
          },
          openPreviewWindow(){
                    const title = encodeURIComponent("");
                    const content = encodeURIComponent(JSON.stringify(this.student_data));

                    const pdfUrl = `http://127.0.0.1:3000/api/pdf_summery_tranfer?title=${title}&content=${content}&year=${this.select_year}`;

                    //  PDF  
                    window.open(pdfUrl, "_blank");
                         
               },
          
     },
     watch: {
          select_year(newYear) {
               console.log("Selected year:", newYear);
               // 
               this.extract_students_by_year();
          }

     },
     mounted() {
          this.fetchStudent_list();
     },
};
</script>