import { createStore } from "vuex";

export default createStore({
  state: {
    user: JSON.parse(localStorage.getItem("user_data")) || null, //  localStorage 
    isSidebarVisible: false, 
  },
  mutations: {
    // 
    setUserData(state, userData) {
      state.user = userData;
      localStorage.setItem("user_data", JSON.stringify(userData)); //  localStorage
    },
    // 
    clearUserData(state) {
      state.user = null;
      localStorage.removeItem("user_data"); //  localStorage 
    },
    TOGGLE_SIDEBAR(state) {
      state.isSidebarVisible = !state.isSidebarVisible;
    },
    SHOW_SIDEBAR(state) {
      state.isSidebarVisible = true;
    },
    HIDE_SIDEBAR(state) {
      state.isSidebarVisible = false;
    },
  },
  actions: {
    setUserData({ commit }, userData) {
      commit("setUserData", userData);
    },
    clearUserData({ commit }) {
      commit("clearUserData");
    },
    toggleSidebar({ commit }) {
      commit("TOGGLE_SIDEBAR");
    },
    showSidebar({ commit }) {
      commit("SHOW_SIDEBAR");
    },
    hideSidebar({ commit }) {
      commit("HIDE_SIDEBAR");
    },
  },
  getters: {
    isAuthenticated(state) {
      return !!state.user;
    },
    getUser(state) {
      return state.user;
    },
    isSidebarVisible(state) {
      return state.isSidebarVisible;
    },
  },
});
