import { createRouter, createWebHistory } from "vue-router";
import homePage from "@/components/home_page.vue";
import loginPage from "@/components/login_page.vue";
import tc_open_subject from "@/components/tc_OPsubject_page.vue";
import tc_adddata_bot from "@/components/tc_adddata_bot.vue";
import tc_standard_plan from "@/components/tc_standardP_page.vue";
import tc_editStandard_plan from "@/components/tc_editStandardplan.vue";
import tc_student_list from "@/components/tc_student_list_page.vue";
import tc_editStudent_plan from "@/components/tc_editStudent_plan.vue";
import tc_submitplan_list from "@/components/tc_submitplan_list.vue";
import tc_inspect_plan from "@/components/tc_inspect_plan.vue";
import std_plan_page from "@/components/std_plan_page.vue";
import std_changePlan_page from "@/components/std_changePlan_page.vue";
import tc_adjust_list from "@/components/tc_adjust_list.vue";
import tc_inspect_adjust from "@/components/tc_inspect_adjust.vue";
import tc_summery_plan from "@/components/tc_summery_plan.vue";
import tc_summery_tranfer from "@/components/tc_summery_tranfer.vue";
import tc_summery_std from "@/components/tc_summery_std.vue";

const routes = [
  { // 
    path: "/",
    name: "homePage", //  string
    component: homePage,
  },
  { // 
    path: "/loginPage",
    name: "loginPage", //  string
    component: loginPage,
  },
  { // 
    path: "/open_subject",
    name: "tc_open_subject", //  string
    component: tc_open_subject,
    meta: { requiresAuth: true }, // 
  },
  { //  Bot
    path: "/data_bot",
    name: "tc_adddata_bot", //  string
    component: tc_adddata_bot,
    meta: { requiresAuth: true }, // 
  },
  {
    // 
    path: "/standard_plan",
    name: "tc_standard_plan",
    component: tc_standard_plan,
    meta: {requiresAuth: true},
  },
  {
    path: "/edit_standard_plan",
    name: "tc_editStandard_plan",
    component: tc_editStandard_plan,
    meta: {requiresAuth: true},
  },
  {
    path: "/student_plan_list",
    name: "tc_student_list",
    component: tc_student_list,
    meta: {requiresAuth: true},
  },
  {
    path: "/edit_student_plan",
    name: "tc_editStudent_plan",
    component: tc_editStudent_plan,
    meta: {requiresAuth: true},
  },
  {
    path: "/submitplan",
    name: "tc_submitplan_list",
    component: tc_submitplan_list,
    meta: {requiresAuth: true},
  },
  {
    path: "/inspectplan",
    name: "tc_inspect_plan",
    component: tc_inspect_plan,
    meta: {requiresAuth: true},
  },
  {
    path: "/student_plan",
    name: "student_plan_page",
    component: std_plan_page,
    meta: {requiresAuth: true},
  },
  {
    path: "/student_change_plan",
    name: "student_changePlan_page",
    component: std_changePlan_page,
    meta: {requiresAuth: true},
  },
  {
    path: "/adjust_list",
    name: "tc_adjust_list",
    component: tc_adjust_list,
    meta: {requiresAuth: true},
  },
  {
    path: "/inspect_adjust",
    name: "tc_inspect_adjust",
    component: tc_inspect_adjust,
    meta: {requiresAuth: true},
  },
  {
    path: "/summery_tranfer",
    name: "tc_summery_tranfer",
    component: tc_summery_tranfer,
    meta: {requiresAuth: true},
  },
  {
    path: "/summery_plan",
    name: "tc_summery_plan",
    component: tc_summery_plan,
    meta: {requiresAuth: true},
  },
  {
    path: "/summery_student",
    name: "tc_summery_std",
    component: tc_summery_std,
    meta: {requiresAuth: true},
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

// Middleware 
router.beforeEach((to, from, next) => {
  const user = localStorage.getItem("user_data"); //  LocalStorage
  if (to.meta.requiresAuth && !user) {
    next("/"); //   homepage
  } else {
    next(); // 
  }
});

export default router;
