<template>
     <MainLayout>
     <div class="form-container">
     <h1></h1>
     <!--  -->
     <table v-if="student_list" class="data-stdplan-table" style="width: 1000px; margin-left: auto; margin-right: auto;" >
          <thead>
               <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
               </tr>
          </thead>
          <tbody>
                    <tr v-for="item in student_list" :key="item.std_id">
                         <td>{{ item.std_id }}</td>
                         <td style="text-align: left;">{{ item.std_name }} {{ item.std_last }}</td>
                         <td>{{ item.batch_year }}</td>
                         <td>{{ item.date_add }}</td>
                         <td>{{ item.date_updated || "-"}}</td>
                         <td>{{ item.user_ap || "-"}} </td>
                         <td v-if="item.status == 1" style="color: green;">{{ convertStatusToText(item.status) }}</td>
                         <td v-if="item.status == 2" style="color: red;">{{ convertStatusToText(item.status) }}</td>
                         <td v-if="item.status == 0" style="color: blue;">{{ convertStatusToText(item.status) }}</td>
                         <td>
                              <button v-if="item.status == 0" class="btn-add" style="font-size: 16px; width: 100px; " @click="inspectAdjust(item.std_id, item.std_name, item.std_last, item.batch_year, item.adjust_id )">
                         <i class="fa-solid fa-magnifying-glass"></i>
                    </button>
                         </td>
                    </tr>
               </tbody>
     </table>
     <p v-if="student_list.length == 0"></p>
     <br>

     </div>
</MainLayout>
</template>

     <script>
     import MainLayout from './main_layout.vue';
     import axios from "axios";
     
     export default {
          name: 'tc_adjust_list',
          components: {
          MainLayout  
          },
          data() {
          return {
               student_list: [],
          };
          },
          methods: {
               async fetchAdjust_list(){
                    try{
                         const response = await axios.get("http://127.0.0.1:3000/api/getAdjust_list"); // URL  API
                         if (response.data.result === "1") {
                              console.log(response.data.datalist);
                              this.student_list = response.data.datalist; //  state
                              const sort_List = this.student_list.sort((a, b) => {
                                   const dateA = this.convert_date(a.date_add); //  ..  ..
                                   const dateB = this.convert_date(b.date_add);
                                   return dateB - dateA; // 
                              });
                              this.student_list = sort_List; //  state
                         } else {
                              // alert("");
                         }
                    }
                    catch (error) {
                         console.error("Error fetching change plan:", error);
                    }
               },
               convert_date(thaiDateStr) { //  ..  ..  JS
                    const [day, month, year] = thaiDateStr.split('-').map(Number);
                    const christianYear = year - 543; //  ..  ..
                    return new Date(christianYear, month - 1, day); //  JS  0
               },
               convertStatusToText(status) {
                    const statusMapping = {
                         1: "",
                         2: "",
                         0: "",
                    };
                    return statusMapping[status] || "";
               },
               async inspectAdjust(std_id, std_name, std_last, year, adjust_id){
                    this.$router.push({
                         name: "tc_inspect_adjust",
                         query: { std_id: std_id,  std_name:std_name, std_last:std_last, year:year, adjust_id:adjust_id}, 
                    });
               }
          },
          mounted() {
          this.fetchAdjust_list();
          },
     };
     </script>
     <style>
     @import "@/assets/css/styles.css";
     </style>