<template>
     <MainLayout>
       <div class="form-container">
          <h1></h1>
         <h2>: {{ std_id }} : {{ std_name }} {{ std_last }} </h2>
          <!--  [, ] -->
          <div v-for="group in groupedPlan" :key="group.yearTerm" class="grouped-table">
          <h2> {{ group.term }}  {{ group.year === 4 ? Number(year) + 1 : year }}</h2>
               <table class="data-stdplan-table">
               <thead>
                    <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    </tr>
               </thead>
               <tbody>
                    <tr v-for="item in group.plans" :key="item.subject_id">
                    <td>{{ item.subject_id }}</td>
                    <td style="text-align: left; width: 450px;">{{ item.subject_name }}</td>
                    <td v-if="item.theory_credits+(item.practical_credits) < 3">
                    {{ item.practical_credits+item.theory_credits }}({{ item.theory_credits }}-{{ item.practical_credits*2 }}-{{ 3-(item.theory_credits+(item.practical_credits*2)) }})
                    </td>
                    <td v-if=" item.theory_credits+(item.practical_credits) >= 3 && item.theory_credits+(item.practical_credits) <= 5 ">
                    {{ item.practical_credits+item.theory_credits }}({{ item.theory_credits }}-{{ item.practical_credits*2 }}-{{ 9-(item.theory_credits+(item.practical_credits*2)) }})
                    </td>
                    <td v-if="item.theory_credits+(item.practical_credits) == 6">
                    {{ item.practical_credits+item.theory_credits }}({{ item.theory_credits }}-{{ item.practical_credits }}-0)
                    </td>
                    <td v-if="!item.group_id">{{ item.subject_group_id }}</td>
                    <td v-if="item.group_id">{{ item.group_id }}</td>
                    </tr>
                    <!--  -->
                    <tr>
                         <td colspan="2" style="text-align: right;">:</td>
                         <td>{{ group.totalCredits }}</td>
                         <td colspan="2"></td>
                    </tr>
               </tbody>
               </table>
                    <br>
                    
          </div>
          <h2>: {{ totalTransferCredits }} : {{ totalAllCredits }} : 125</h2>
          
          <h4 style="font-weight: lighter;"> ***  
               <br> (--)</h4>
           <div  class="btn-group"  v-if="status != 1 && status != 4"
               style="
                         display: flex;
                         justify-content: center;
                         gap: 20px;">
               <div>
               <!--  Popup -->
               <button class="btn-remove" style="font-size: 16px; margin-right: 10px; width: 150px;" @click="openRejectPopup">
                    <i class="fa-solid fa-xmark"></i> 
               </button>

               <!-- Popup  -->
               <div v-if="showRejectPopup" class="popup-overlay">
                    <div class="popup-content">
                    <h3></h3>
                    <textarea v-model="rejectReason" rows="4" cols="50" style="font-size: 18px;"></textarea>
                    <br>
                    <button class="btn-remove" style="font-size: 18px; background-color: #02bd2a; margin-right: 10px;" @click="rejectPlan"></button>
                    <button class="btn-remove" style="font-size: 18px; background-color: #a3a3a3;" @click="closeRejectPopup"></button>
                    </div>
               </div>
               </div>
               
               <button class="btn-add" style="font-size: 16px; background-color: limegreen;  width: 150px;" @click="submitPlan">
                    <i class="fa-solid fa-check"></i> 
               </button>
          </div>
          <div class="btn-group" style="margin-bottom: 100px; gap:500px">
               <button class="btn-back" style="font-size: 16px;" @click="goBack">
                    <i class="fa-solid fa-arrow-left-long"></i> 
               </button>
               <button v-if="status != 1" class="btn-back" style="font-size: 16px; background-color: blue; margin-right: 25px;" @click="editPlan">
                    <font-awesome-icon :icon="['fas', 'file-pen']" /> 
               </button>
               <button v-if="status == 1" class="btn-back" style="font-size: 16px; background-color: #00cc00; width: 200px;" @click="openPreviewWindow">
                    <i class="fa-solid fa-file-pdf"></i>     
               </button>
         </div>

     </div>
     </MainLayout>
   </template>

<script>
import MainLayout from './main_layout.vue';
import axios from 'axios';

export default {
     name: "tc_inspect_plan",
     components: {
          MainLayout
     },
     data(){
          return{
               std_id: this.$route.query.std_id,
               stdplan_id: this.$route.query.stdplan_id,
               status: this.$route.query.status,
               std_name:this.$route.query.std_name,
               std_last:this.$route.query.std_last,
               year:this.$route.query.year,
               std_planlist: [],
               term:"",
               groupedPlan: [],
               selectGroup:"",
               group_list:[],
               subject_list:[],
               selectsubject:"",
               showRejectPopup: false, // 
               rejectReason: "",        // 
               transfer_list:[],
          };
     },
     computed: {
          calculateTotalCredits() {
               return (plans) => {
               return plans.reduce((sum, plan) => {
                    const totalCredits = plan.theory_credits + plan.practical_credits;
                    return sum + totalCredits;
               }, 0);
               };
          },
          totalAllCredits() {
               return this.groupedPlan.reduce((sum, group) => sum + group.totalCredits, 0);
          },
                    //  practical credits
                    totalPracticalCredits() {
               return this.transfer_list.reduce(
                    (sum, item) => sum + Number(item.practical_credits || 0), 0
               );
          },
          //  theory credits
          totalTheoryCredits() {
               return this.transfer_list.reduce(
                    (sum, item) => sum + Number(item.theory_credits || 0), 0
               );
          },
          //  practical + theory credits 
          totalTransferCredits() {
               return this.totalPracticalCredits + this.totalTheoryCredits;
          },
     },
     methods:{
          async fetchStudent_plan(){
               try{
                    const response = await axios.post("http://127.0.0.1:3000/api/getStudentplan_list", {
                         std_id: this.std_id
                    });
                    if (response.data.result === "1") {
                         console.log(response.data.datalist);
                         this.std_planlist = response.data.datalist; //  state
                         this.groupPlans(); // 
                              
                    } else {
                         alert("");
                    }
               }
               catch (error) {
                    console.error("Error fetching subjects:", error);
               }
          },
          async fetchTransfer_list(){ // 
               try{
                    const response = await axios.post("http://127.0.0.1:3000/api/getTranfer_plan",{
                         std_id: this.std_id
                    }); // URL  API
                    if(response.data.result == "1"){
                         console.log("..--..",response.data.datalist);
                         this.transfer_list = response.data.datalist; //  transfer_list
                    }
                    else if(response.data.result == "0"){
                         this.transfer_list = [];
                    }

               }
               catch(error){
                    console.error("Error fetching subject:", error);  
               }
          },

          groupPlans() {
               // 
               const yearTermPairs = [
               [3, 1],
               [3, 2],
               [3, 3],
               [4, 1],
               [4, 2],
               ];

               //  [, ]
               this.groupedPlan = yearTermPairs.map(([year, term]) => {
                    const plans = this.std_planlist.filter(plan => plan.year === year && plan.term === term);
                    const totalCredits = plans.reduce((sum, plan) => {
                         if((sum + (plan.theory_credits + plan.practical_credits)) > 22){
                              this.spacialTerm = true;
                         }
                         return sum + (plan.theory_credits + plan.practical_credits); 
                    }, 0);
               return {
                    yearTerm: `${year}-${term}`, //  key 
                    year,
                    term: term === 3 ? "" : term, //  3
                    plans: this.std_planlist.filter(plan => plan.year === year && plan.term === term),
                    totalCredits,
               };
               });
          },
          async submitPlan(){ // 
               if(confirm("")){
                    try {
                         const response = await axios.post("http://127.0.0.1:3000/api/updateStudent_plan", {
                              std_id:this.std_id,
                              stdplan_id: this.stdplan_id,
                              status: 1
                         });
                         
                         if (response.data.result == "1") {
                              alert("");
                              this.$router.push('/submitplan')
                         } 

                         } catch (error) {
                         console.error("Error fetching subject:", error);
                    }

               }

          },
          //  Popup
          openRejectPopup() {
               this.showRejectPopup = true;
          },
          //  Popup
          closeRejectPopup() {
               this.showRejectPopup = false;
          },
          async rejectPlan(){ // 
               if(confirm("")){
                    try {
                         const response = await axios.post("http://127.0.0.1:3000/api/updateStudent_plan", {
                              std_id:this.std_id,
                              stdplan_id: this.stdplan_id,
                              reason: this.rejectReason,
                              status: 2
                         });
                         
                         if (response.data.result == "1") {
                              alert("");
                              this.$router.push('/submitplan')
                         } 
                         
                         } catch (error) {
                         console.error("Error fetching subject:", error);
                    }

               }

          },
          async editPlan(){
                    this.$router.push({
                         name: "tc_editStudent_plan", //  route  router/index.js
                         query: { std_id: this.std_id, stdplan_id: this.stdplan_id, std_name:this.std_name, std_last:this.std_last, year:this.year}, //  query
                    });
               },
               openPreviewWindow() {
               const std_id = this.std_id;
               const std_name = this.std_name;
               const std_last = this.std_last;
               const year = this.year;

               const title = encodeURIComponent("");
               const content = encodeURIComponent(JSON.stringify(this.std_planlist));

               const pdfUrl = `http://127.0.0.1:3000/api/pdf_generate?title=${title}&content=${content}
               &std_id=${std_id}&std_name=${std_name}&std_last=${std_last}&year=${year}`;

               //  PDF  
               window.open(pdfUrl, "_blank");
          },  
          goBack() {
          // 
               this.$router.go(-1)
          },

     },
          watch: {
          selectGroup(newValue) {
               console.log("Group ID changed:", newValue);
               this.fetchSubject(); //  fetchSubject  selectGroup 
          },
     },
     mounted() {
          this.fetchStudent_plan();
          this.fetchTransfer_list();
     }
}
</script>