<template>
  <div id="app">
    <!--  -->
    <MainLayout v-if="user">
      <!--   MainLayout -->
      <router-view />
    </MainLayout>
    <router-view v-else /> <!--   router-view -->
  </div>
</template>

<script>
import MainLayout from "./components/main_layout.vue";
import { mapState, mapActions } from "vuex"; //  mapState  mapActions  Vuex

export default {
  name: "App",
  components: {
    MainLayout, //  MainLayout 
  },
  computed: {
    ...mapState(["user"]), //  state user  Vuex
  },
  created() {
    this.loadUserData(); // 
  },
  methods: {
    ...mapActions(["setUserData", "clearUserData"]), //  action  Vuex

    //  LocalStorage  Vuex
    loadUserData() {
      const userData = localStorage.getItem("user_data");
      if (userData) {
        this.setUserData(JSON.parse(userData)); //  Vuex
      }
    },

    //  LocalStorage  Vuex
    logout() {
      this.clearUserData(); //  Vuex
      localStorage.removeItem("user_data"); //  LocalStorage
      this.$router.push("/"); //  homepage
    },
  },
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
