<template>
     <MainLayout>
          <div class="form-container">
          <h1></h1>
          <h2>: {{ plan_id }} : {{ batch_year }}</h2>
          <!-- Popup  -->
          <div v-if="change_popup" class="popup-overlay">
                  <div class="popup-content">
                  <h3> {{ sub_name }} </h3>
                  <div class="dropdown-container" style=" width: 600px;">
                       <select  v-model="select_year" id="year">
                           <option value="" disabled></option>     
                                   <option value="3"> 3</option>
                                   <option value="4"> 4</option>
                         </select>
                         
                         <select v-model="select_term" id="term">
                              <option value="" disabled></option>    
                              <template v-for="item in open_subject" :key="item.subject_id">
                                   <option v-if="item.subject_id === sub_id">{{ item.term }}</option>
                              </template>
                         </select>
                    </div>
                    <button class="btn-remove"
                    style="font-size: 18px; background-color: #00ff00; margin-right: 10px;"
                    @click="update_plan"></button>
                    <button class="btn-remove" style="font-size: 18px; background-color: #a3a3a3;" @click="closePopup"></button>
                  </div>
            </div>

          <!--  -->
               <div v-for="group in groupedPlan" :key="group.yearTerm" class="grouped-table">
                    <h3> {{ group.term }}  {{ group.year === 4 ? Number(batch_year) + 1 : batch_year }}</h3>
                    <table class="subject-table">
                         <thead>
                              <tr>
                                   <th style=" width: 120px;"></th>
                                   <th style=" width: 450px;"></th>
                                   <th></th>
                                   <th></th>
                                   <th></th>
                              </tr>
                         </thead>
                         <tbody>
                              <tr v-for="item in group.plans" :key="item.code">
                                   <td>{{ item.subject_id }}</td>
                                   <td style="text-align: left;">{{ item.subject_name }}</td>
                                   <td v-if="item.theory_credits+(item.practical_credits) < 3">
                                   {{ item.practical_credits+item.theory_credits }}({{ item.theory_credits }}-{{ item.practical_credits*2 }}-{{ 3-(item.theory_credits+(item.practical_credits*2)) }})
                                   </td>
                                   <td v-if=" item.theory_credits+(item.practical_credits) >= 3 && item.theory_credits+(item.practical_credits) <= 5 ">
                                   {{ item.practical_credits+item.theory_credits }}({{ item.theory_credits }}-{{ item.practical_credits*2 }}-{{ 9-(item.theory_credits+(item.practical_credits*2)) }})
                                   </td>
                                   <td v-if="item.theory_credits+(item.practical_credits) == 6">
                                   {{ item.practical_credits+item.theory_credits }}({{ item.theory_credits }}-{{ item.practical_credits }}-0)
                                   </td>
                                   <td v-if="item.group_id">{{ item.group_id }}</td>
                                   <td>
                                   <div class="btn-group" style="gap: 5px;">
                                   <button class="btn-remove" 
                                        @click="openChangePopup(item.subject_name, item.subject_id)" 
                                        style="background-color: #00ccff; margin-right: 10px;">
                                        <i class="fa-solid fa-repeat"></i>
                                   </button>
                                   <button class="btn-remove" @click="del_Subject(item.subject_id, group.term, group.year)">
                                        <i class="fa-regular fa-trash-can"></i></button>
                                   </div>
                                   </td>
                              </tr>
                              <tr>
                                   <td colspan="2" style="text-align: right;">:</td>
                                   <td>{{ group.totalCredits }}</td>
                                   <td colspan="2"></td>
                              </tr>
                         </tbody>
                    </table>
                   
                    <br>
               <!--  combobox  -->
               <div class="dropdown-container" >
               <select v-model="selectGroup" @change="fetchStructure" 
                    style="margin-right: 10px;" 
                    :disabled="group.totalCredits >= 22 && spacialTerm">
                    <option value="" disabled></option>

                    <option v-for="item in filteredGroups(group.term)" :key="item.group_id" :value="item.group_id">
                         {{ item.group_id + " " + item.group_name }}
                    </option>
               </select>
               <select v-model="selectsubject" @change="fetchOpen_subject" 
                         style="margin-right: 10px;" 
                         :disabled="!selectGroup ||(group.totalCredits >= 22 && spacialTerm)">
                    <option value="" disabled></option>
                    
                    <template v-for="item in group.open" :key="item.subject_id">
                         <option v-if="selectGroup == item.group_id" :value="item.subject_id">
                              {{ item.subject_name }}
                         </option>
                    </template>
               </select>
               <select v-model="selectGroup_id" @change="fetchSubject" style="width: 150px; margin-right: 10px;" :disabled="!selectsubject || (group.totalCredits >= 22 && spacialTerm == true)">
                    <option value="" disabled></option>
                    <option>{{ selectGroup }}</option>
                    <option v-if="selectGroup != 2.2"> 3.1</option>
               </select>
               <button v-if="(group.totalCredits  < 25 && spacialTerm == false) || !(group.totalCredits >= 22 && spacialTerm == true)" type="submit" class="submit-stdplan-button" style="font-size: 20px;" @click="addSubject(group.term, group.year, group)" :disabled="!selectGroup_id">
                    <i class="fa-solid fa-plus"></i>
               </button>
               </div>
               
     </div>
     <h3>: {{ totalAllCredits }}</h3>
     <div class="btn-group" style="margin-bottom: 20px;">
          <button class="btn-back" @click="del_StandardPlan(plan_id, batch_year)"  style="background-color: red;">
               <i class="fa-regular fa-trash-can"> </i>
          </button>
          <button class="btn-back" style=" background-color:  #3385ff;" @click="resetChanges">
               <i class="fa-solid fa-arrow-rotate-left"> </i>
          </button>
          <button class="btn-back" @click="saveChanges"  style=" background-color: #33cc33;">
               <i class="fa-solid fa-floppy-disk"> </i>
          </button>
     </div>
     <div class="btn-group">
          <button class="btn-back" style="font-size: 20px;" @click="goBack">
               <i class="fa-solid fa-arrow-left-long"> </i>
          </button>
     </div>

     <h4 style="font-weight: lighter; margin-bottom: 100px;"> *** 
          <br> (--)</h4>
</div>
</MainLayout>
</template>

<script>
import MainLayout from './main_layout.vue';
import axios from 'axios';

export default {
name: "tc_editStandard_plan",
components: {
       MainLayout  
     },
data() {
     return {
     plan_id: this.$route.query.plan_id, 
     batch_year: this.$route.query.batch_year, 
     subjects: [], 
     data_list: [],
     open_list:[],
     groupedPlan: [],
     subject_list:[],
     group_list:[],
     selectGroup:"",
     selectsubject:"",
     selectGroup_id:"",
     spacialTerm:false,
     open_subject:[],
     temp_standrad_list:[],
     change_popup: false,
     sub_name: "",
     sub_id: "",
     select_term: "",
     select_year: "",
     sort_open_list: [],

     };
},
computed: {
     calculateTotalCredits() {
          return (plans) => {
               return plans.reduce((sum, plan) => {
                    const totalCredits = plan.theory_credits + plan.practical_credits;
                    return sum + totalCredits;
               }, 0);
          };
     },
     totalAllCredits() {
        return this.groupedPlan.reduce((sum, group) => sum + group.totalCredits, 0);
     },
     filteredGroups() {
        return (term) => { if(term == ""){
                              term = 3
                         }
            return this.group_list.filter(item => 
                this.sort_open_list.some(openItem => openItem.group_id === item.group_id
                 && openItem.term == term)
            );
        };
    },
},
methods: {
     //  Popup
     openChangePopup(sub_name,sub_id) {
          this.sub_name = sub_name;
          this.sub_id = sub_id;
          this.change_popup = true;
     },
          //  Popup
     closePopup() {
          this.change_popup = false;
     },
     goBack() {
          const old_data = this.data_list
          const new_data = this.temp_standrad_list
          if(JSON.stringify(old_data) !== JSON.stringify(new_data)){
               if(confirm(" ?")){
                    this.$router.push('/standard_plan')
               }
          }
          else{
               this.$router.push('/standard_plan')
          }
     },
     async fetchOpen_subject(){
          console.log(this.selectGroup)
          try{
               const response = await axios.post("http://127.0.0.1:3000/api/getopensubject",{
                         
                    }); 
                    if(response.data.result == "1"){
                         this.open_subject = response.data.datalist;
                         this.sort_openlist();
                         this.groupPlans();
                         
                    }
                    else if(response.data.result == "2"){
                         this.open_subject = [];
                         this.sort_openlist();
                         this.groupPlans();
                         
                    }
          }
          catch(error){
               console.error("Error fetching subject:", error);
          }
     },
     async fetchStandard_list(){ //  standardList
          try {
                    const response = await axios.post("http://127.0.0.1:3000/api/getStandard_list",{
                         plan_id: this.plan_id
                    }); 
                    if(response.data.result == "1"){
                         console.log(response.data.datalist);
                         this.data_list = response.data.datalist;
                         this.temp_standrad_list = JSON.parse(JSON.stringify(this.data_list));
                         this.sort_openlist();
                         this.groupPlans();
                    }
                    else if(response.data.result == "2"){
                         this.data_list = [];
                         this.sort_openlist();
                         this.groupPlans();
                    }
               } catch (error) {
                    console.error("Error fetching subject:", error);
               }
     },
     async fetchStructure(){ //
               try {
                    const response = await axios.get("http://127.0.0.1:3000/api/getStructure",{
                    }); // URL  API
                    this.group_list = response.data.datalist;
               } catch (error) {
                    console.error("Error fetching subject:", error);
               }
     },
     async fetchSubject() {
          try {
               if (!this.selectGroup) {
                    this.subject_list = []; // 
                    return;
               }
               
               const response = await axios.post("http://127.0.0.1:3000/api/getSubject", {
                    group_id: this.selectGroup, 
               });
               
               if (response.data.result == "1") {
                    this.subject_list = response.data.datalist;
               } else {
                    alert("");
               }
               } catch (error) {
               console.error("Error fetching subject:", error);
          }
     },
     sort_openlist(){
          //  open_subject  temp_planlist
          this.sort_open_list = this.open_subject.filter(item => {
               return !this.temp_standrad_list.some(tempItem => tempItem.subject_id === item.subject_id);
          });
     },
     async addSubject(term, year, group){ //  standardPlanList
          let totalCredit = this.calculateTotalCredits(group.plans);
          let selectData = this.open_subject.find(item => item.subject_id === String(this.selectsubject));
          let selectSubject = this.temp_standrad_list.find(item => item.subject_id === String(this.selectsubject));
               if(selectSubject){
                    alert("");
                    return;
               }
          let pCredits = Number(selectData.practical_credits);
          let tCredits = Number(selectData.theory_credits);
          let newPcredits = totalCredit + pCredits;
          let newTcredits = totalCredit + tCredits;

          if(confirm("")){
               // console.log(this.plan_id);
               // console.log(this.selectsubject);
               // console.log(this.subject_list);
               // console.log(selectData);
               // console.log(pCredits);
               // console.log(tCredits);
               // console.log(totalCredit);
               // console.log(newPcredits);
               // console.log(newTcredits);

               
               // 
               if (newPcredits > 25 || newTcredits >25 ) {
                    alert("  25");
                    return;
               }
               if((newPcredits > 22|| newTcredits > 22) && this.spacialTerm == true){
                    alert("  25 ");
                    return;
               }
               if (newPcredits > 22 || newTcredits > 22) {
                    if (!confirm(" 22 ?")) {
                         return;
                    }
               }
               if(term == ""){
                              term = 3
                         }
               //  group_id, practical_credits, subject_id, subject_nameTh, theory_credits,
               const newPlan = {
                    group_id:this.selectGroup_id,
                    subject_id:selectData.subject_id,
                    term: term,
                    year: year,
                    practical_credits: selectData.practical_credits,
                    theory_credits: selectData.theory_credits,
                    subject_name: selectData.subject_name,
               };
               //  temp_planlist
               this.temp_standrad_list.push(newPlan); //  spread operator  
               this.sort_openlist();
               this.groupPlans(); //  groupPlans 
          }
     },
     update_plan() {
                // 
               let filteredPlans = this.temp_standrad_list.filter(
                    plan => plan.year === Number(this.select_year) && plan.term === Number(this.select_term));
               
               // /
               console.log("Filtered Plans:", filteredPlans);

               //  Practical Credits  Theory Credits 
               let totalPcredits = filteredPlans.reduce((sum, plan) => sum + Number(plan.practical_credits || 0), 0);
               let totalTcredits = filteredPlans.reduce((sum, plan) => sum + Number(plan.theory_credits || 0), 0);

               let selectData = this.open_subject.find(item => item.subject_id === String(this.sub_id));
               if (!selectData) {
                    alert(" ");
                    return;
               }

               let pCredits = Number(selectData.practical_credits);
               let tCredits = Number(selectData.theory_credits);
               let totalCredits = totalPcredits + totalTcredits;
               let newPcredits = totalCredits + pCredits;
               let newTcredits = totalCredits + tCredits;

               console.log(`New Practical Credits: ${newPcredits}, New Theory Credits: ${newTcredits}`);

               if(confirm("")){
                    // 
                    if (newPcredits > 25 || newTcredits >25 ) {
                         alert("  25");
                         return;
                    }
                    // if((newPcredits > 22|| newTcredits > 22) && this.spacialTerm == true){
                    //      alert("  25 ");
                    //      return;
                    // }
                    if (newPcredits >= 22 || newTcredits > 22) {
                         if (!confirm(" 22 ?")) {
                              return;
                         }
                    }
                    //  temp_planlist
                    let existingPlan = this.temp_standrad_list.find(item => item.subject_id === this.sub_id);
                    if (existingPlan) {
                         //  year  term 
                         existingPlan.year = Number(this.select_year);
                         existingPlan.term = Number(this.select_term);
                         existingPlan.hasChanged = true; // 
                         console.log(" temp_planlist ");
                         this.change_popup = false;
                    } else {
                         alert("");
                    }
                    this.sort_openlist();
                    this.groupPlans(); // 

                              }
          },
     async del_Subject(subject_id, term, year){
          if(term == ""){
                    term = 3
               }
               
               if (confirm("?")) {
                    //  temp_planlist  subject_id, year, term
                    this.temp_standrad_list = this.temp_standrad_list.filter(
                              (plan) => !(plan.subject_id === subject_id && plan.term === term)
                    );
                    this.sort_openlist();
                    this.groupPlans(); //  groupPlans 
                    console.log(` subject_id=${subject_id}, year=${year}, term=${term} `);
               }
     },
     resetChanges() {
               this.temp_standrad_list = JSON.parse(JSON.stringify(this.data_list)); // 
               this.sort_openlist();
               this.groupPlans(); // 
               alert("!"); // 
          },
     async saveChanges() {
               const oldData = this.data_list;
               const newData = this.temp_standrad_list;
               if (JSON.stringify(oldData) === JSON.stringify(newData)) {
                    alert("");
                    return;
               }
               if (confirm("?")) {

                    try {
                              //  temp_planlist  array 
                              let planData = Array.isArray(this.temp_standrad_list) ? this.temp_standrad_list : [this.temp_standrad_list];
                              console.log("planData:", planData);
                              //  array  API
                              const response = await axios.post("http://127.0.0.1:3000/api/setStandard_list", {
                              plan_id: this.plan_id,
                              plans: planData,
                              });

                              if (response.data.result === "1") {
                                   alert("!");
                                   //  std_planlist  temp_planlist
                                   this.data_list = JSON.parse(JSON.stringify(this.temp_standrad_list));
                                   this.$router.push("/standard_plan")
                              } else {
                                   alert("");
                              }
                    } catch (error) {
                              console.error("Error saving changes:", error);
                    }
               }
     },
     async del_StandardPlan(){
          if(confirm("")){
               try {
                    //  API  subject_id 
                    const response = await axios.post("http://127.0.0.1:3000/api/dropStandard_plan", {
                         plan_id: this.plan_id,
                         batch_year: this.batch_year,
                    });
               
                    //  API
                    console.log(response.data);
               
                    // 
                    if (response.data.result === "1") {
                         alert("!");
                         this.$router.push('/standard_plan');
                    } else {
                         alert("");
                    }
                    } catch (error) {
                    console.error("Error adding subject:", error);
               }
          }
     },
     groupPlans() {
               this.spacialTerm = false;
               // 
               const yearTermPairs = [
               [3, 1],
               [3, 2],
               [3, 3],
               [4, 1],
               [4, 2],
               ];

               //  [, ]
               this.groupedPlan = yearTermPairs.map(([year, term]) => {
                    
                    const plans = this.temp_standrad_list.filter(plan => plan.year === year && plan.term === term);
                    const totalCredits = plans.reduce((sum, plan) => {
                         if((sum + (plan.theory_credits + plan.practical_credits)) > 22){
                              this.spacialTerm = true;
                         }
                         return sum + (plan.theory_credits + plan.practical_credits); 
                    }, 0);
               return console.log(this.spacialTerm), {
                    yearTerm: `${year}-${term}`, //  key 
                    year,
                    term: term === 3 ? "" : term, //  3
                    plans: this.temp_standrad_list.filter(plan => plan.year === year && plan.term === term),
                    totalCredits, // 
                    open: this.sort_open_list.filter(plan => plan.term === term),
               };
               });
          },
},
     watch: {
          selectGroup(newValue) {
               console.log("Group ID changed:", newValue);
               this.selectGroup_id = newValue;
               this.fetchOpen_subject(); //  fetchSubject  selectGroup 
               this.selectsubject = "";
               this.groupPlans();
          },

     },
     mounted() {
          // :  API
          console.log("Plan ID:", this.plan_id);
          console.log("Batch Year:", this.batch_year);
          console.log(this.spacialTerm);
          this.fetchStandard_list();
          this.fetchOpen_subject();
          this.fetchStructure();
          this.fetchSubject();
     },
};
</script>

