<template>
     <MainLayout>
          
       <div class="form-container">
          <h1></h1>
          <h2>: {{ std_id }} : {{ std_name }} {{ std_last }}</h2>
          
          <div v-if="status == 2" class="fixed-box">
               <h3>: {{ reason }}</h3>
          </div>
          <select v-if="status == 0" v-model="selectPlan" @change="fetchPlan_list">
               <option value="" disabled></option>
               <template v-for="item in plans_list" :key="item.plan_id">
               <option v-if="Number(year) == Number(item.batch_year)" :value="item.plan_id">
                         {{ item.plan_id }}
                    </option>
               </template>
         </select>
         <!-- Popup  -->
         <div v-if="change_popup" class="popup-overlay">
                  <div class="popup-content">
                  <h3> {{ sub_name }} </h3>
                  <div class="dropdown-container" style=" width: 600px;">
                       <select  v-model="select_year" id="year">
                           <option value="" disabled></option>     
                                   <option value="3"> 3</option>
                                   <option value="4"> 4</option>
                         </select>
                         
                         <select v-model="select_term" id="term">
                              <option value="" disabled></option>    
                              <template v-for="item in open_subject" :key="item.subject_id">
                                   <option v-if="item.subject_id === sub_id">{{ item.term }}</option>
                              </template>
                         </select>
                    </div>
                    <button class="btn-remove"
                    style="font-size: 18px; background-color: #00ff00; margin-right: 10px;"
                    @click="update_plan"></button>
                    <button class="btn-remove" style="font-size: 18px; background-color: #a3a3a3;" @click="closePopup"></button>
                  </div>
            </div>
         <!--  [, ] -->
               <div v-for="group in groupedPlan" :key="group.yearTerm" class="grouped-table">
               <h2> {{ group.term }}  {{ group.year === 4 ? Number(year) + 1 : year }}</h2>
                    <table class="data-stdplan-table">
                    <thead>
                         <tr>
                         <th style=" width: 120px;"></th>
                         <th style=" width: 450px;"></th>
                         <th></th>
                         <th></th>
                         <th v-if="!group"></th>
                         </tr>
                    </thead>
                    <tbody>
                         <tr v-for="item in group.plans" :key="item.subject_id">
                         <td>{{ item.subject_id }}</td>
                         <td style="text-align: left;" v-if="item.subject_name">{{ item.subject_name }}</td>
                         <td style="text-align: left;" v-if="!item.subject_name">{{ item.subject_nameTh }}</td>
                         <td v-if="item.theory_credits+(item.practical_credits) < 3">
                         {{ item.practical_credits+item.theory_credits }}({{ item.theory_credits }}-{{ item.practical_credits*2 }}-{{ 3-(item.theory_credits+(item.practical_credits*2)) }})
                         </td>
                         <td v-if=" item.theory_credits+(item.practical_credits) >= 3 && item.theory_credits+(item.practical_credits) <= 5 ">
                         {{ item.practical_credits+item.theory_credits }}({{ item.theory_credits }}-{{ item.practical_credits*2 }}-{{ 9-(item.theory_credits+(item.practical_credits*2)) }})
                         </td>
                         <td v-if="item.theory_credits+(item.practical_credits) == 6">
                         {{ item.practical_credits+item.theory_credits }}({{ item.theory_credits }}-{{ item.practical_credits }}-0)
                         </td>
                         <td>{{ item.group_id }}</td>
                         <td>
                         <div class="btn-group" style="gap: 5px;">
                              <button class="btn-remove" 
                                   @click="openChangePopup(item.subject_name, item.subject_id)" 
                                   style="background-color: #00ccff; margin-right: 10px;">
                                   <i class="fa-solid fa-repeat"></i>
                              </button>
                              
                              <!--  -->
                              <button class="btn-remove" @click="removeFromTemp_data(item.subject_id, group.year, group.term)">
                                   <i class="fa-regular fa-trash-can"></i></button>
                              <!--  status  0  -->
                              <!-- <button v-if="status == 0" class="btn-remove" @click="del_data(item.subject_id, group.year, group.term)">
                                   <i class="fa-regular fa-trash-can"></i> !!!</button> -->
                         </div>
                         </td>
                         </tr>
                         <!--  -->
                         <tr>
                                   <td colspan="2" style="text-align: right;">:</td>
                                   <td>{{ group.totalCredits }}</td>
                                   <td colspan="2"></td>
                         </tr>
                    </tbody>
                    </table>

                    <br>
                    <!--  combobox  -->
                    <div class="dropdown-container">
                    <select v-model="selectGroup" @change="fetchStructure" style="margin-right: 10px;" :disabled="group.totalCredits >= 22 && spacialTerm == true">
                         <option value="" disabled></option>
                         <option v-for="item in filteredGroups(group.term)" :key="item.group_id" :value="item.group_id">
                         {{ item.group_id + " " + item.group_name }}
                         </option>
                         
                    </select>
                    <select  v-model="selectsubject" @change="fetchOpen_subject" style="margin-right: 10px;" :disabled="!selectGroup || (group.totalCredits >= 22 && spacialTerm == true)">
                         <option value="" disabled></option>
                         <template v-for="item in group.open" :key="item.subject_id">
                         <option v-if="selectGroup == item.group_id" :value="item.subject_id">
                              {{ item.subject_name }}
                         </option>
                         </template>
                    </select>
                    <select  v-model="selectGroup_id" @change="fetchSubject" style="width: 150px; margin-right: 10px;" :disabled="(!selectsubject || (group.totalCredits >= 22 && spacialTerm == true)) ">
                         <option value="" disabled></option>
                         <option>{{ selectGroup }}</option>
                         <option v-if="selectGroup != 2.2"> 3.1</option>
                    </select>
                    <!--  -->
                    <button v-if="((group.totalCredits  < 25 && spacialTerm == false) || !(group.totalCredits >= 22 && spacialTerm == true))"
                    type="submit" class="submit-stdplan-button" style="font-size: 20px;" 
                    @click="addToTemp_data(group.year, group.term, group)">
                         <i class="fa-solid fa-plus"></i></button>
                    </div>
               </div>
          
         <h4 style="font-weight: lighter;"> ***  
          <br> (--)</h4>
          <div class="btn-group">
               <button class="btn-back" style="font-size: 20px; width: 200px;" @click="goBack">
                    <i class="fa-solid fa-arrow-left-long"> </i>
               </button>
               <button v-if="status != 1" class="btn-back" style="font-size: 20px; width: 200px; background-color:  #3385ff;" @click="resetChanges">
                    <i class="fa-solid fa-arrow-rotate-left"> </i>
               </button>               
               <button v-if="status != 1" class="btn-back" style="font-size: 20px; width: 200px; background-color: #33cc33;" @click="saveChanges">
                    <i class="fa-solid fa-floppy-disk"> </i>
               </button>
          </div>
          <h2>: {{ totalTransferCredits }} : {{ totalAllCredits }} : 125</h2>
     <h2></h2>
     <table class="data-stdplan-table" style="margin-bottom: 100px;">
          <thead>
          <tr>
               <th></th>
               <th></th>
               <th></th>
               <th></th>
               <th></th>
               <th></th>
               
          </tr>
          </thead>
          <tbody>
          <template v-for="(group, category) in credit_status" :key="category">
          <!--  (sort) -->
          <tr>
            <td colspan="7" style="text-align: left;"><strong>{{ category }}</strong></td>
          </tr>
          <!--  -->
          <template v-for="item in group" :key="item._id">
          <tr v-if="item.status == ''" style="color: #ff1a1a;">
               <td style="text-align: left; ">{{item.group_id +" "+ item.group_name }}</td>
               <td>{{ item.required_credits }}</td>
               <td>{{ item.tranfer_credits }}</td>
               <td>{{ item.need_credit }}</td>
               <td>{{ item.earned_credits }}</td>
               <td>{{ item.credit_isneed}}</td>

          </tr>
          <tr v-if="item.status == '' && item.required_credits == item.earned_credits" style="color: #33cc33;">
               <td style="text-align: left;">{{ item.group_id +" "+ item.group_name }}</td>
               <td>{{ item.required_credits }}</td>
               <td>{{ item.tranfer_credits }}</td>
               <td>{{ item.need_credit }}</td>
               <td>{{ item.earned_credits }}</td>
               <td>{{ item.credit_isneed}}</td>
          </tr>
          <tr v-if="item.earned_credits > item.required_credits" style="color: #ffcc00;">
               <td style="text-align: left;">{{ item.group_id +" "+ item.group_name }}</td>
               <td>{{ item.required_credits }}</td>
               <td>{{ item.tranfer_credits }}</td>
               <td>{{ item.need_credit }}</td>
               <td>{{ item.earned_credits }}</td>
               <td>{{ item.credit_isneed}}</td>
          </tr>
          </template>
        </template>
          </tbody>
     </table>
       </div>
     </MainLayout>
   </template>
   
<script>
import MainLayout from './main_layout.vue';
import axios from 'axios';

export default {
     name: "tc_editStudent_plan",
     components: {
          MainLayout
     },
     computed: {
          calculateTotalCredits() {
               return (plans) => {
               return plans.reduce((sum, plan) => {
                    const totalCredits = plan.theory_credits + plan.practical_credits;
                    return sum + totalCredits;
               }, 0);
               };
          },
          totalAllCredits() {
               return this.groupedPlan.reduce((sum, group) => sum + group.totalCredits, 0);
          },
          filteredGroups() {
               return (term) => { if(term == ""){
                                        term = 3
                                   }
                    return this.group_list.filter(item => 
                         this.sort_open_list.some(openItem => openItem.group_id === item.group_id
                         && openItem.term == term)
                    );
               };
          },
          credit_status() {
                //  reduce()  `sort`
               return this.group_list.reduce((acc, item) => {
               if (!acc[item.sort]) {
                    acc[item.sort] = [];
               }
               acc[item.sort].push(item);
               return acc;
               }, {});
          },
           //  practical credits
          totalPracticalCredits() {
               return this.transfer_list.reduce(
                    (sum, item) => sum + Number(item.practical_credits || 0), 0
               );
          },
          //  theory credits
          totalTheoryCredits() {
               return this.transfer_list.reduce(
                    (sum, item) => sum + Number(item.theory_credits || 0), 0
               );
          },
          //  practical + theory credits 
          totalTransferCredits() {
               return this.totalPracticalCredits + this.totalTheoryCredits;
          },
     },
     data(){
          return{
               std_id: this.$route.query.std_id,
               stdplan_id: this.$route.query.stdplan_id,
               std_name:this.$route.query.std_name,
               std_last:this.$route.query.std_last,
               year:this.$route.query.year,
               status:this.$route.query.status,
               reason:this.$route.query.reason,
               std_planlist: [],
               temp_planlist:[],
               term:"",
               groupedPlan: [],
               selectGroup:"",
               group_list:[],
               subject_list:[],
               plans_list:[],
               selectPlan:"",
               selectsubject:"",
               selectGroup_id:"",
               spacialTerm: false,
               open_subject:[],
               transfer_list:[],
               change_popup: false,
               sub_name: "",
               sub_id: "",
               select_term: "",
               select_year: "",
               sort_open_list: [],
               
          };
     },
     methods:{
          //  Popup
          openChangePopup(sub_name,sub_id) {
               this.sub_name = sub_name;
               this.sub_id = sub_id;
               this.change_popup = true;
          },
          //  Popup
          closePopup() {
               this.change_popup = false;
          },
          async fetchOpen_subject(){ // 
          console.log(this.selectGroup)
          try{
               const response = await axios.post("http://127.0.0.1:3000/api/getopensubject",{
                         
                    }); 
                    if(response.data.result == "1"){
                         this.open_subject = response.data.datalist;
                         this.sort_openlist(); 
                         this.groupPlans();
                         
                    }
                    else if(response.data.result == "2"){
                         this.open_subject = [];
                         this.sort_openlist();
                         this.groupPlans();
                         
                    }
          }
          catch(error){
               console.error("Error fetching subject:", error);
          }
          },
          async fetchPlan_list(){
               try {
                    const response = await axios.get("http://127.0.0.1:3000/api/getStandard_plan",{
                    }); // URL  API
                    this.plans_list = response.data.datalist; //  state

               } catch (error) {
                    console.error("Error fetching subject:", error);
               }
          },
          async fetchTransfer_list(){ // 
               try{
                    const response = await axios.post("http://127.0.0.1:3000/api/getTranfer_plan",{
                         std_id: this.std_id
                    }); // URL  API
                    if(response.data.result == "1"){
                         console.log("..--..",response.data.datalist);
                         this.transfer_list = response.data.datalist; //  transfer_list
                    }
                    else if(response.data.result == "0"){
                         this.transfer_list = [];
                    }

               }
               catch(error){
                    console.error("Error fetching subject:", error);  
               }
          },
          async fetchStandardPlan_list() {
               this.std_planlist = []; //  
               try {
                    const response = await axios.post("http://127.0.0.1:3000/api/getStandard_list", {
                         plan_id: this.selectPlan
                    });

                    if (response.data.result == "1") {
                         console.log(response.data.datalist);
                         this.std_planlist = response.data.datalist;
                         //  temp_planlist  transfer_list
                         this.std_planlist = this.std_planlist.filter(tempItem => 
                              !this.transfer_list.some(transferItem => 
                                   tempItem.subject_id === transferItem.subject_id
                              )
                         );
                         this.temp_planlist = JSON.parse(JSON.stringify(this.std_planlist)); // Clone 

                         this.groupPlans(); // 

                    } else if (response.data.result == "2") {
                         this.std_planlist = [];
                         this.groupPlans();
                    }
               } catch (error) {
                    console.error("Error fetching subject:", error);
               }
          },
          async fetchStudent_plan(){
               try{
                    const response = await axios.post("http://127.0.0.1:3000/api/getStudentplan_list", {
                         std_id: this.std_id
                    });
                    if (response.data.result === "1") {
                         console.log(response.data.datalist);
                         this.std_planlist = response.data.datalist; //  state
                         this.temp_planlist = JSON.parse(JSON.stringify(this.std_planlist)); // clone 
                         this.sort_openlist(); 
                         this.groupPlans(); // 

                         //  status
                         // if (this.status !== 0) {
                         //      this.groupPlans(); //  std_planlist
                         // } else {
                         //      this.temp_planlist = []; // 
                         // }
                              
                    } else if(response.data.result === "0"){
                         this.std_planlist = [];
                         this.temp_planlist = []; // 
                         this.groupPlans();
                    }
                    
               }
               catch (error) {
                    console.error("Error fetching subjects:", error);
               }
          },
          async fetchStructure(){
               try {
                    const response = await axios.get("http://127.0.0.1:3000/api/getStructure",{
                         
                    }); // URL  API
                    this.group_list = response.data.datalist; //  state
                    this.mergeCredits(); // 
               } catch (error) {
                    console.error("Error fetching subject:", error);
               }
          },
          async fetchSubject() {
               try {
                    if (!this.selectGroup) {
                         this.subject_list = []; // 
                         return;
                    }
                    
                    const response = await axios.post("http://127.0.0.1:3000/api/getSubject", {
                         group_id: this.selectGroup, //  selectGroup  API
                    });
                    
                    if (response.data.result == "1") {
                         this.subject_list = response.data.datalist; //  state
                    } else {
                         alert("");
                    }
                    } catch (error) {
                    console.error("Error fetching subject:", error);
               }
          },
          sort_openlist(){
               //  open_subject  temp_planlist
               this.sort_open_list = this.open_subject.filter(item => {
                    return !this.temp_planlist.some(tempItem => tempItem.subject_id === item.subject_id);
               });
          },
          addToTemp_data(year, term, group) {
               if(this.selectsubject == "" || this.selectGroup == ""){
                    alert("");
                    return;

               }
               let totalCredit = this.calculateTotalCredits(group.plans);
               let selectData = this.open_subject.find(item => item.subject_id === String(this.selectsubject));
               let selectSubject = this.temp_planlist.find(item => item.subject_id === String(this.selectsubject));
               if(selectSubject){
                    alert("");
                    return;
               }
               let pCredits = Number(selectData.practical_credits);
               let tCredits = Number(selectData.theory_credits);
               let newPcredits = totalCredit + pCredits;
               let newTcredits = totalCredit + tCredits;
               if(confirm("")){
                    // 
                    if (newPcredits > 25 || newTcredits >25 ) {
                         alert("  25");
                         return;
                    }
                    if((newPcredits > 22|| newTcredits > 22) && this.spacialTerm == true){
                         alert("  25 ");
                         return;
                    }
                    if (newPcredits >= 22 || newTcredits > 22) {
                         if (!confirm(" 22 ?")) {
                              return;
                         }
                    }
                    //  group_id, practical_credits, subject_id, subject_nameTh, theory_credits,
                    const newPlan = {
                         group_id:this.selectGroup_id,
                         subject_id:selectData.subject_id,
                         term: term,
                         year: year,
                         practical_credits: selectData.practical_credits,
                         theory_credits: selectData.theory_credits,
                         subject_name: selectData.subject_name,
                    };
                    //  temp_planlist
                    this.temp_planlist.push(newPlan); //  spread operator  
                    this.sort_openlist(); //  sort_openlist  open_subject
                    this.groupPlans(); //  groupPlans 
                    this.mergeCredits(); // 
                    console.log(" temp_planlist ");
                    
               }
          },
          update_plan() {
                // 
               let filteredPlans = this.temp_planlist.filter(
                    plan => plan.year === Number(this.select_year) && plan.term === Number(this.select_term));
               
               // /
               console.log("Filtered Plans:", filteredPlans);

               //  Practical Credits  Theory Credits 
               let totalPcredits = filteredPlans.reduce((sum, plan) => sum + Number(plan.practical_credits || 0), 0);
               let totalTcredits = filteredPlans.reduce((sum, plan) => sum + Number(plan.theory_credits || 0), 0);

               let selectData = this.open_subject.find(item => item.subject_id === String(this.sub_id));
               if (!selectData) {
                    alert(" ");
                    return;
               }

               let pCredits = Number(selectData.practical_credits);
               let tCredits = Number(selectData.theory_credits);
               let totalCredits = totalPcredits + totalTcredits;
               let newPcredits = totalCredits + pCredits;
               let newTcredits = totalCredits + tCredits;

               console.log(`New Practical Credits: ${newPcredits}, New Theory Credits: ${newTcredits}`);

               if(confirm("")){
                    // 
                    if (newPcredits > 25 || newTcredits >25 ) {
                         alert("  25");
                         return;
                    }
                    // if((newPcredits > 22|| newTcredits > 22) && this.spacialTerm == true){
                    //      alert("  25 ");
                    //      return;
                    // }
                    if (newPcredits >= 22 || newTcredits > 22) {
                         if (!confirm(" 22 ?")) {
                              return;
                         }
                    }
                    //  temp_planlist
                    let existingPlan = this.temp_planlist.find(item => item.subject_id === this.sub_id);
                    if (existingPlan) {
                         //  year  term 
                         existingPlan.year = Number(this.select_year);
                         existingPlan.term = Number(this.select_term);
                         existingPlan.hasChanged = true; // 
                         console.log(" temp_planlist ");
                         this.change_popup = false;
                    } else {
                         alert("");
                    }
                    this.sort_openlist();
                    this.groupPlans(); // 
                    this.mergeCredits(); // 
                              }
          },
          removeFromTemp_data(subject_id, year, term) {
               if(term == ""){
                    term = 3
               }
               
               if (confirm("?")) {
                    //  temp_planlist  subject_id, year, term
                    this.temp_planlist = this.temp_planlist.filter(
                              (plan) => !(plan.subject_id === subject_id && plan.term === term)
                    );
                    this.sort_openlist();
                    this.groupPlans(); //  groupPlans 
                    console.log(` subject_id=${subject_id}, year=${year}, term=${term} `);
               }
          },
          resetChanges() {
               this.temp_planlist = JSON.parse(JSON.stringify(this.std_planlist)); // 
               this.sort_openlist();
               this.groupPlans(); // 
               alert("!"); // 
          },

          async saveChanges() {
               const oldData = this.std_planlist; // 
               const newData = this.temp_planlist; // 
               if (JSON.stringify(oldData) === JSON.stringify(newData)) {
                    alert("");
                    return;
               }

                  if (confirm("?")) {
                         let status = 3;
                         //  ""  credit_status 
                         const hasIncompleteStatus = Object.values(this.credit_status).some(group =>
                              group.some(item => item.status === '')
                         );

                         if (hasIncompleteStatus) {
                              if (!confirm(" ?")) {
                                   return;
                              }
                                   status = 4;    
                         }
                         try {
                               //  temp_planlist  array 
                               let planData = Array.isArray(this.temp_planlist) ? this.temp_planlist : [this.temp_planlist];
                                   console.log("planData:", planData);
                               //  array  API
                               const response = await axios.post("http://127.0.0.1:3000/api/updateStudentplan_list", {
                                     std_id: this.std_id,
                                     plans: planData,
                                     status: status  
                               });

                               if (response.data.result === "1") {
                                     alert("!");
                                     //  std_planlist  temp_planlist
                                     this.std_planlist = JSON.parse(JSON.stringify(this.temp_planlist));
                                     this.$router.push("/student_plan_list")
                               } else {
                                     alert("");
                               }
                         } catch (error) {
                               console.error("Error saving changes:", error);
                         }
                  }
          },
          groupPlans() {
               // 
               const yearTermPairs = [
               [3, 1],
               [3, 2],
               [3, 3],
               [4, 1],
               [4, 2],
               ];
               //  [, ]
               this.groupedPlan = yearTermPairs.map(([year, term]) => {
                    const plans = this.temp_planlist.filter(plan => plan.year === year && plan.term === term);
                    const totalCredits = plans.reduce((sum, plan) => {
                         if((sum + (plan.theory_credits + plan.practical_credits)) > 22){
                              this.spacialTerm = true;
                         }
                         
                         return sum + (plan.theory_credits + plan.practical_credits);
                    }, 0);
               return {
                    yearTerm: `${year}-${term}`, //  key 
                    year,
                    term: term === 3 ? "" : term, //  3
                    plans: this.temp_planlist.filter(plan => plan.year === year && plan.term === term),
                    totalCredits,
                    //  open_subject  open
                    open: this.sort_open_list.filter(plan => plan.term === term),
               };
               });
          },
          mergeCredits() {
               this.group_list = this.group_list.map(group => {
                    //  group_id
                    let plans = this.temp_planlist.filter(plan => String(plan.group_id) === String(group.group_id));
                    let tranfers_plan = this.transfer_list.filter(plan => String(plan.group_id) === String(group.group_id));

                    // 
                    let totalCredits_need = plans.reduce((sum, plan) => sum + (plan.theory_credits + plan.practical_credits), 0);
                    let totalCredits_tranfer = tranfers_plan.reduce((sum, plan) => sum + (plan.theory_credits + plan.practical_credits), 0);
                    let totalCredits_all = totalCredits_need + totalCredits_tranfer;

                    let credit_need = group.credit - totalCredits_all;
                    if (credit_need < 0) {
                         credit_need = 0;
                    }
                    // 
                    let status = totalCredits_all >= group.credit ? "" : "";

                    return {
                         ...group, //  group
                         _id: Date.now().toString(), //  ID 
                         required_credits: group.credit, // 
                         tranfer_credits: totalCredits_tranfer, // 
                         need_credit: totalCredits_need, // 
                         earned_credits: totalCredits_all, // 
