<template>
     <MainLayout>
     <div class="form-container">
     <h1></h1>
     <select v-model="select_year" style="font-size: 20px;">
          <option value="" disabled></option>
          <option v-for="y in year" :key="y" :value="y">{{ y }}</option>    
     </select>
    <!--  -->
    <table class="data-stdplan-table" style="width: 800px; margin-left: auto; margin-right: auto;" >
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in plans_list" :key="index">
          <td style="width: 400px; text-align: left; ">   {{ item.plan_id }}</td>
          <td style="width: 400px;">{{ item.batch_year }}</td>
          <td>
               <div class="btn-group">
                    <button class="btn-add" @click="editPlan(item.plan_id, item.batch_year)" style="width: 100px;">
                         <i class="fa-solid fa-magnifying-glass"></i>
                    </button>
               </div>
          </td>
        </tr>
      </tbody>
    </table>
    <br>
     <!--  -->    
    <div class="dropdown-container" style="width: 700px; margin-left: auto; margin-right: auto;">
     
          <input type="text" 
          id="plan_id" 
          style="font-size: 16px; margin-right: 10px;" 
          v-model="plan_id" 
          placeholder="" required />
     
     
          <input type="text" 
          id="batch_year"
          style="font-size: 16px; margin-right: 10px;"
          v-model="batch_year"
          placeholder="" required />
          
     <button type="submit" class="submit-stdplan-button" style="
     font-size: 18px;   
     margin-left: auto;
     margin-right: auto;
     display: block;
     padding: 10px 30px"
     @click="addPlan">
          
     </button>
    </div>
  </div>
     </MainLayout>
</template>


<script>
     import MainLayout from './main_layout.vue'
     import axios from "axios";
     export default {
          name: "tc_standard_plan",
          components: {
               MainLayout
          },
          data() {
               return {
                    plan_id: "",
                    batch_year: "",
                    dataList: [], // 
                    year: [],
                    select_year:"",
                    plans_list: [], // 
               };
          },
          methods:{
               async fetchStandardPlan(){
                    try {
                         const response = await axios.get("http://127.0.0.1:3000/api/getStandard_plan",{
                         }); // URL  API
                         this.dataList = response.data.datalist; //  state
                         this.extract_year(); // 
                    } catch (error) {
                         console.error("Error fetching subject:", error);
                    }
               },
               extract_year(){
                    const allYears = this.dataList.map(s => s.batch_year); //  year  student
                    this.year = [...new Set(allYears)].sort((a, b) => b - a); //  Set
               },
               extract_plans_by_year() {
                    const plans = this.dataList.filter(data =>
                         data.batch_year === this.select_year
                    );
                    this.plans_list = plans
               },
               async addPlan(){
                    if(confirm("")){
                         if (this.plan_id === "" || this.batch_year === "") {
                              alert("!");
                              return;
                         }

                         try {
                              //  API  subject_id 
                              const response = await axios.post("http://127.0.0.1:3000/api/setStandard_plan", {
                              plan_id:this.plan_id,
                              batch_year:this.batch_year,
                              });
                         
                              //  API
                              console.log(response.data);
                         
                              // 
                              if (response.data.result === "1") {
                                   const response = await axios.get("http://127.0.0.1:3000/api/getStandard_plan",{
                                   }); // URL  API
                                        this.dataList = response.data.datalist; //  state
                                        this.$router.push({
                                             name: "tc_editStandard_plan",
                                             query: { plan_id: this.plan_id, batch_year: this.batch_year },
                                        });
                              } else {
                              alert("!");
                              }
                         } catch (error) {
                              console.error("Error adding subject:", error);
                         }
                    }
               },
               async editPlan(plan_id, batch_year){
                    //  plan_id  term_year
                    this.$router.push({
                         name: "tc_editStandard_plan", //  route  router/index.js
                         query: { plan_id: plan_id, batch_year: batch_year }, //  query
                    });

               },

          },
          watch: {
               select_year(newYear) {
                    console.log("Selected year:", newYear);
                    this.extract_plans_by_year(); // 
               },
          },
          mounted() {
               //  mount
               this.fetchStandardPlan();
          },
     }
</script>