<template>
<MainLayout>
<div class="subject-table-page">
    <br>      
    <h1></h1>
      <select v-model="selectedSemester" style="font-size: 20px;">
        <option :key="term1" :value="1">1</option>
        <option :key="term2" :value="2">2</option>
        <option :key="term" :value="3"></option>
      </select>
    <p v-if="selectedSemester == 3" style="font-size: 24px; font-weight: bold;"> </p>
    <p v-else style="font-size: 24px; font-weight: bold;"> {{ selectedSemester }}</p>
    <!--  -->
    <table v-if="subjects.length" class="subject-table">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in subjects" :key="item.code">
          <td>{{ item.subject_id }}</td>
          <td style="text-align: left; width: 370px;">{{ item.subject_name }}</td>
          <td v-if="item.theory_credits+(item.practical_credits) < 3">
            {{ item.practical_credits+item.theory_credits }}({{ item.theory_credits }}-{{ item.practical_credits*2 }}-{{ 3-(item.theory_credits+(item.practical_credits*2)) }})
          </td>
          <td v-if=" item.theory_credits+(item.practical_credits) >= 3 && item.theory_credits+(item.practical_credits) <= 5 ">
            {{ item.practical_credits+item.theory_credits }}({{ item.theory_credits }}-{{ item.practical_credits*2 }}-{{ 9-(item.theory_credits+(item.practical_credits*2)) }})
          </td>
          <td v-if="item.theory_credits+(item.practical_credits) == 6">
            {{ item.practical_credits+item.theory_credits }}({{ item.theory_credits }}-{{ item.practical_credits }}-0)
          </td>
          <td>{{ item.group_id }}</td>
          <td>
            <button class="btn-remove" @click="del_openSubject(item.subject_id)"><i class="fa-regular fa-trash-can"></i></button>
          </td>
        </tr>
      </tbody>
    </table>
    <p v-else></p>
    <br>
    <div class="dropdown-container">
      <select v-model="selectGroup" @change="fetchStructure" style="margin-right: 10px;">
        <option value="" disabled></option>
          <option v-for="item in group_list" :key="item.group_id" :value="item.group_id">
            {{ item.group_id +" "+ item.group_name }}
          </option>
      </select>
      <select v-model="selectsubject" @change="fetchSubject" style="margin-right: 10px;" :disabled="!selectGroup">
          <option value="" disabled></option>
          <option v-for="item in subject_list" :key="item.subject_id" :value="item.subject_id">
            {{ item.subject_nameTh }}
          </option>
        </select>
        <button class="submit-stdplan-button" style="font-size: 20px;" @click="addSubject"><i class="fa-solid fa-plus"></i></button>
      </div>
      <h4 style="font-weight: lighter;"> ***  
        <br> (--)</h4>
      <br><br><br>
  </div>
</MainLayout>
</template>

<script>
import MainLayout from './main_layout.vue';
import axios from "axios";


export default {
  name: 'tc_open_subject',
  components: {
    MainLayout  
  },

  data() {
    return {
      subjects: [], // 
      subject_list: [], // 
      selected_course: "", // 
      course_list:[],
      selectedSemester: 1, //  1
      selectGroup: "",
      selectsubject:"",
      group_list: [],
      group_id: '',
    };
  },
  methods: {
    async fetchSubjectlist() {
      // 
      if (!this.selectedSemester) return;
      try {
        const response = await axios.post("http://127.0.0.1:3000/api/getopensubject",{
          term: this.selectedSemester
        }); 
        if(response.data.result == "1"){
            this.subjects = response.data.datalist; //  state
          }
        else if(response.data.result == "2"){
            this.subjects = [];
        }
      } catch (error) {
        console.error("Error fetching subjects:", error);
      }
    },
    async fetchSubject() {
      try {
          console.log(this.group_id);
          const response = await axios.post("http://127.0.0.1:3000/api/getSubject",{
            group_id: this.selectGroup
          }); // URL  API
          console.log(response.data); //  API
          if(response.data.result == "1"){
            this.subject_list = response.data.datalist; //  state
            
          }
        } catch (error) {
          console.error("Error fetching subject:", error);
        }
      },
    async fetchStructure(){
      try {
          const response = await axios.get("http://127.0.0.1:3000/api/getStructure",{
          }); // URL  API
          this.group_list = response.data.datalist; //  state
        } catch (error) {
          console.error("Error fetching subject:", error);
        }
    },
    async addSubject(subject_id) {
      if(confirm("")){
        try {
          // 
          console.log("Subject ID :", subject_id);
      
          //  API  subject_id 
          const response = await axios.post("http://127.0.0.1:3000/api/setOpensubject", {
            term: this.selectedSemester,
            subject_id:this.selectsubject,
            group_id:this.selectGroup
          });
      
          //  API
          console.log(response.data);
          // 
          if (response.data.result === "1") {
            const resetResponse = await axios.post("http://127.0.0.1:3000/api/getopensubject",{
            open_id: this.selectedOpen_id,
            term: this.selectedSemester
            });
              if(resetResponse.data.result === "1"){
                this.subjects = resetResponse.data.datalist
              }
          } 
          else if(response.data.result ==="2"){
            alert("! ");
          }
          else{
            alert("!");
          }
        } catch (error) {
          console.error("Error adding subject:", error);
        }
    }
},

async del_openSubject(subject_id){
  if(confirm("")){
    try {
        //  API  subject_id 
        const response = await axios.post("http://127.0.0.1:3000/api/drop_open", {
          term:this.selectedSemester ,
          subject_id: subject_id
        });
    
        //  API
        console.log(response.data);
    
        // 
        if (response.data.result === "1") {
          const resetResponse = await axios.post("http://127.0.0.1:3000/api/getopensubject",{
          open_id: this.selectedOpen_id,
          term: this.selectedSemester
          });
            if(resetResponse.data.result === "1"){
              this.subjects = resetResponse.data.datalist
            }
            else if(resetResponse.data.result === "2"){
              this.subjects = [];
            }

        } else {
          alert("");
        }
      } catch (error) {
        console.error("Error adding subject:", error);
      }
  }
},
  },
  watch: {
          selectedSemester(newValue) {
            console.log("Group ID changed:", newValue);
            this.fetchSubjectlist(); 
          },
          selectGroup(newValue) {
            console.log("Group ID changed:", newValue);
            this.fetchSubject(); //  fetchSubject  selectGroup 
          },
     },
  mounted() {
    //  mount
    this.fetchSubjectlist();
    this.fetchSubject();
    this.fetchStructure();
  },
};
</script>
<!--  CSS  -->
<style>
@import "@/assets/css/styles.css";
</style>
