<template>
     <MainLayout>
          
          <div class="form-container">
          <h1></h1>
          <div class="dropdown-container">
               <select v-model="select_year" style="font-size: 20px;">
                    <option value="" disabled></option>
                    <option v-for="y in year" :key="y" :value="y">{{ y }}</option>
               </select>
               <select v-model="select_status" style="font-size: 20px;" :disabled="!select_year" >
                    <option value=""></option>
                    <option value="0"></option>
                    <option value="1"></option>
                    <option value="2"></option>
                    <option value="3"></option>
                    <option value="4"></option>
               </select>
          </div>
          <h2></h2>
          <table class="student-table" v-if="student_data.length">
               <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in sortedStudents" :key="item.std_id">
               <td>{{ item.std_id }}</td>
               <td style="text-align: left;">{{ item.std_name }} {{ item.std_last }}</td>
               <td>{{ item.batch_year }}</td>
               <td v-if="item.status == 1" style="color: green;">{{ convertStatusToText(item.status) }}</td>
               <td v-if="item.status == 2" style="color: red;">{{ convertStatusToText(item.status) }}</td>
               <td v-if="item.status == 3" style="color: orange;">{{ convertStatusToText(item.status) }}</td>
               <td v-if="item.status == 4" style="color: red;">{{ convertStatusToText(item.status) }}</td>
               <td v-if="item.status == 0" style="color: blue;">{{ convertStatusToText(item.status) }}</td>
               <td>
                    <button v-if="item.status != 0" class="btn-add" style="font-size: 16px; width: 100px;" @click="inspectPlan(item.std_id, item.stdplan_id, item.status, item.std_name, item.std_last, item.batch_year)">
                         <i class="fa-solid fa-magnifying-glass"></i>
                    </button>
               </td>
          </tr>
        </tbody>
          </table>
          <div v-if="!student_data.length">
               <h3></h3>
          </div>
          <!-- <button class="btn-back" style="font-size: 16px; background-color: #00cc00; width: 200px;" @click="openPreviewWindow" v-if="student_data.length">
                    <i class="fa-solid fa-file-pdf"></i>     
          </button> -->
     </div>
     </MainLayout>
</template>

<script>
     import MainLayout from './main_layout.vue';
     import axios from "axios";
     export default {
          name: "tc_submitplan_list",
          components: {
               MainLayout
          },
          data() {
               return {
                    students: [], // 
                    year: [], // 
                    student_data: [], // 
                    sortedStudents: [], // 
                    select_year: "", //  dropdown
                    select_status: "", //  dropdown
               };
          },
          methods: {
               async fetchStudent_list(){
                    try{
                         const response = await axios.get("http://127.0.0.1:3000/api/getStudent_plan"); // URL  API
                         if (response.data.result === "1") {
                              console.log(response.data.datalist);
                              this.students = response.data.datalist; //  state
                              this.extract_year(); // 
                         } 
                    }
                    catch(error){
                         console.error("Error fetching student data:", error);
                    }
               },
               extract_year(){
                    const allYears = this.students.map(s => s.batch_year); //  year  student
                    this.year = [...new Set(allYears)].sort((a, b) => b - a); //  Set
               },
               extract_students_by_year() {
                    const student = this.students.filter(student =>
                         student.batch_year === this.select_year
                    );
                    this.student_data = student
                    this.sort_by_status(); // 
               },
               sort_by_status(){
                    if (this.select_status === "") {
                         const sortedStudents = this.students.filter(student =>
                              student.batch_year === this.select_year
                         );
                         this.sortedStudents = sortedStudents
                    }
                    else { 
                              const sortedStudents = this.student_data.filter(student =>
                              Number(student.status) === Number(this.select_status)
                         );
                         this.sortedStudents = sortedStudents
                    }
               },
               convertStatusToText(status) { //  status  string  
                    
                    const statusMapping = {
                         1: "",
                         2: "",
                         3: "",
                         4: "",
                         0: "",
                    };
                    return statusMapping[status] || "";
               },
               async inspectPlan(std_id, stdplan_id, status, std_name, std_last, year){ // 
                    console.log(status)
                    if(status == 0){
                         status = "0"
                    }
                    else if(status == 2){
                         status = "2"
                    }
                    else if(status == 3){
                         status = "3"
                    }
                    else if(status == 4){
                         status = "4"
                    }
                    else{
                         status = "1"
                    }
                    console.log(status);
                    this.$router.push({
                         name: "tc_inspect_plan", 
                         query: { std_id: std_id, stdplan_id: stdplan_id, status: status, std_name: std_name, std_last: std_last, year:year}, //  query
                    });
               },
               openPreviewWindow(){
                    //  status 
                    const confirmedStudents = this.sortedStudents.filter(student => student.status === 1);
                    console.log(" status = 1:", confirmedStudents);

                    const status = 1
                    const title = encodeURIComponent("");
                    const content = encodeURIComponent(JSON.stringify(confirmedStudents));

                    const pdfUrl = `http://127.0.0.1:3000/api/pdf_generate?title=${title}&content=${content}&status=${status}`;

                    //  PDF  
                    window.open(pdfUrl, "_blank");
                         
               },
          },
          watch: {
               select_year(newYear) {
                    console.log("Selected year:", newYear);
                    // 
                    this.extract_students_by_year();
                    this.select_status = "";
                    this.sort_by_status(); //  
               },
               select_status(newValue){
                    console.log("Selected year:", newValue);
                    this.sort_by_status(); // 
               }
          },
          mounted() {
          this.fetchStudent_list();
          
          },
     }
</script>
