<template>
     <MainLayout>
       <div class="form-container">
          <h1></h1>
         <h2>: {{ user.std_id }} : {{ user.std_name }} {{ user.std_last }}</h2>

          <!--  [, ] -->
          <div v-for="group in groupedPlan" :key="group.yearTerm" class="grouped-table">
          <h2> {{ group.term }}  {{ group.year === 4 ? Number(user.batch_year) + 1 : user.batch_year }}</h2>
               <table class="data-stdplan-table">
               <thead>
                    <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    </tr>
               </thead>
               <tbody>
                    <tr v-for="item in group.plans" :key="item.subject_id">
                    <td>{{ item.subject_id }}</td>
                    <td style="text-align: left; width: 450px;">{{ item.subject_name }}</td>
                    <td v-if="item.theory_credits+(item.practical_credits) < 3">
                    {{ item.practical_credits+item.theory_credits }}({{ item.theory_credits }}-{{ item.practical_credits*2 }}-{{ 3-(item.theory_credits+(item.practical_credits*2)) }})
                    </td>
                    <td v-if=" item.theory_credits+(item.practical_credits) >= 3 && item.theory_credits+(item.practical_credits) <= 5 ">
                    {{ item.practical_credits+item.theory_credits }}({{ item.theory_credits }}-{{ item.practical_credits*2 }}-{{ 9-(item.theory_credits+(item.practical_credits*2)) }})
                    </td>
                    <td v-if="item.theory_credits+(item.practical_credits) == 6">
                    {{ item.practical_credits+item.theory_credits }}({{ item.theory_credits }}-{{ item.practical_credits }}-0)
                    </td>
                    <td v-if="!item.group_id">{{ item.subject_group_id }}</td>
                    <td v-if="item.group_id">{{ item.group_id }}</td>
                    </tr>
                         <!--  -->
                         <tr>
                              <td colspan="2" style="text-align: right;">:</td>
                              <td>{{ group.totalCredits }}</td>
                              <td colspan="2"></td>
                         </tr>
               </tbody>
               </table>
          </div>
          <h4 style="font-weight: lighter;"> ***  
               <br> (--)</h4>
          <h2>: {{ totalTransferCredits }} : {{ totalAllCredits }} : {{ totalTransferCredits + totalAllCredits }}</h2>
          <div class="btn-group">
               <button class="btn-back" style="font-size: 16px; background-color: #00a3cc; width: 200px;" @click="goToChangePlan">
                    
               </button>
               <button class="btn-back" style="font-size: 16px; background-color: #00cc00; width: 200px;" @click="openPreviewWindow">
                    <i class="fa-solid fa-file-pdf"></i>     
               </button>
          </div>

          <h2></h2>
          <table class="data-stdplan-table" style="margin-bottom: 100px;">
               <thead>
               <tr>
                    <th></th>
                    <th></th>
                    <th></th>

               </tr>
               </thead>
               <tbody>
               <!--  -->
               <tr v-for="item in transfer_list" :key="item._id">
                    <td>{{ item.subject_id }}</td>
                    <td style="text-align: left;">{{ item.subject_name }}</td>
                    <td>{{ item.practical_credits + item.theory_credits }}</td>
               </tr>

               </tbody>
          </table>
     </div>

     </MainLayout>
   </template>

<script>
import { inject, computed } from "vue";
import MainLayout from './main_layout.vue';
import axios from 'axios';

export default {
     setup() {
          const user = inject("user"); //  user  MainLayout.vue
          const userData = computed(() => user?.value || {}); //  undefined

          console.log("User Data:", userData.value); // 

          return { user: userData };
     },
     name: "student_plan_page",
     components: {
          MainLayout
     },
     data(){
          return{
               std_id:"",
               stdplan_id: "",
               status: "",
               std_name:"",
               std_last:"",
               year:"",
               std_planlist: [],
               groupedPlan: [],
               group_list:[],
               transfer_list:[],
               adjust_list: [],

          };
     },
     computed: {

          totalAllCredits() {
               return this.groupedPlan.reduce((sum, group) => sum + group.totalCredits, 0);
          },
          //  practical credits
          totalPracticalCredits() {
               return this.transfer_list.reduce(
                    (sum, item) => sum + Number(item.practical_credits || 0), 0
               );
          },
          //  theory credits
          totalTheoryCredits() {
               return this.transfer_list.reduce(
                    (sum, item) => sum + Number(item.theory_credits || 0), 0
               );
          },
          //  practical + theory credits 
          totalTransferCredits() {
               return this.totalPracticalCredits + this.totalTheoryCredits;
          },
     },
     methods:{
          async fetchStudent_plan(){
               try{
                    const response = await axios.post("http://127.0.0.1:3000/api/getStudentplan_list", {
                         std_id: this.user.std_id,
                    });
                    if (response.data.result === "1") {
                         console.log(response.data.datalist);
                         this.std_planlist = response.data.datalist; //  state
                         this.groupPlans(); // 
                              
                    } 
               }
               catch (error) {
                    console.error("Error fetching subjects:", error);
               }
          },
          async fetchTransfer_list(){ // 
               try{
                    const response = await axios.post("http://127.0.0.1:3000/api/getTranfer_plan",{
                         std_id: this.user.std_id
                    }); // URL  API
                    if(response.data.result == "1"){
                         console.log("..--..",response.data.datalist);
                         this.transfer_list = response.data.datalist; //  transfer_list
                    }
                    else if(response.data.result == "0"){
                         this.transfer_list = [];
                    }

               }
               catch(error){
                    console.error("Error fetching subject:", error);  
               }
          },
          async fetchAdjust_list(){
               try{
                    const response = await axios.post("http://127.0.0.1:3000/api/getAdjust_list",{
                    std_id: this.user.std_id,
                    }); // URL  API
                    if (response.data.result === "1") {
                         console.log(response.data.datalist);
                         this.adjust_list = response.data.datalist; //  state
                         
                    } else {
                         // alert("");
                    }
               }
               catch{
                    // console.error("Error fetching student data:", error);
               }
          },
          groupPlans() {
               // 
               const yearTermPairs = [
               [3, 1],
               [3, 2],
               [3, 3],
               [4, 1],
               [4, 2],
               ];

               //  [, ]
               this.groupedPlan = yearTermPairs.map(([year, term]) => {
                    const plans = this.std_planlist.filter(plan => plan.year === year && plan.term === term);
                    const totalCredits = plans.reduce((sum, plan) => {
                         if((sum + (plan.theory_credits + plan.practical_credits)) > 22){
                              this.spacialTerm = true;
                         }
                         return sum + (plan.theory_credits + plan.practical_credits); 
                    }, 0);
               return {
                    yearTerm: `${year}-${term}`, //  key 
                    year,
                    term: term === 3 ? "" : term, //  3
                    plans: this.std_planlist.filter(plan => plan.year === year && plan.term === term),
                    totalCredits,
               };
               });
          },
          openPreviewWindow() {
               const std_id = this.user.std_id;
               const std_name = this.user.std_name;
               const std_last = this.user.std_last;
               const year = this.user.batch_year;

               const title = encodeURIComponent("");
               const content = encodeURIComponent(JSON.stringify(this.std_planlist));

               const pdfUrl = `http://127.0.0.1:3000/api/pdf_generate?title=${title}&content=${content}
               &std_id=${std_id}&std_name=${std_name}&std_last=${std_last}&year=${year}`;

               //  PDF  
               window.open(pdfUrl, "_blank");
          },     

          goToChangePlan() {
               const adjust_status = this.adjust_list.find(item => item.status === 0);
               if(adjust_status){
                    alert("");
                    return;
               }
               else{
                    this.$router.push("/student_change_plan");
               }
          },    
     },
     mounted() {
          this.fetchStudent_plan();
          this.fetchTransfer_list();
          this.fetchAdjust_list();
     //      this.fetchStructure();
     //      this.fetchSubject();
          
     }
}
</script>