<template>
  <div>
    <!-- Header -->
    <header>
      <nav class="navbar">
        <div class="navbar-left">
          <span class="menu-icon" @click="toggleSidebar">
            <i class="fas fa-bars menu-icon" style="color: blue;"></i>
          </span>
          <span class="navbar-title"></span>
        </div>
        <!--  user  -->
        <div class="navbar-right" v-if="user && (isAdmin || isOfficer || isTeacher)">
          <div class="user-info">
            <p>{{ user.user_name }} {{ user.user_last }}</p>
            <p>Status: {{ getStatusLabel(user.user_status) }}</p>
          </div>
          <button class="logout-button" @click="logout">Log out</button>
        </div>

        <div class="navbar-right" v-if="user && isStudent">
          <div class="user-info">
            <p>{{ user.std_name }} {{ user.std_last }}</p>
            <p>Status: {{ getStatusLabel(user.user_status) }}</p>
          </div>
          <button class="logout-button" @click="logout">Log out</button>
        </div>

        <!--  -->
        <router-link to="/loginPage" v-if="!user">
          <button class="login-button"></button>
        </router-link>
      </nav>

      <!-- Sidebar  -->
      <transition name="slide" style="text-align: left;">
        <div :class="['sidebar', { 'sidebar-active': isSidebarVisible }]" v-if="isStudent">
          <ul>
            <router-link to="/"><li></li></router-link>
            <router-link to="/student_plan"><li></li></router-link>
          </ul>
        </div>
      </transition>
      <transition name="slide" style="text-align: left;">
        <div :class="['sidebar', { 'sidebar-active': isSidebarVisible }]" v-if="isOfficer">
          <ul>
            <router-link to="/"><li></li></router-link>
            <router-link to="/open_subject"><li></li></router-link>
            <router-link to="/standard_plan"><li></li></router-link>
            <router-link to="/student_plan_list"><li></li></router-link>
          </ul>
        </div>
      </transition>
      <transition name="slide" style="text-align: left;">
        <div :class="['sidebar', { 'sidebar-active': isSidebarVisible }]" v-if="isAdmin  || isTeacher">
          <ul class="list-group">
            <router-link to="/"><li></li></router-link>
            <router-link to="/open_subject"><li></li></router-link>
            <router-link to="/standard_plan"><li></li></router-link>
            <router-link to="/student_plan_list"><li></li></router-link>
            <router-link to="/submitplan"><li></li></router-link>
            <router-link to="/adjust_list"><li></li></router-link>
            <label style="background-color: #8c8c8c; padding: 2px 1px;"></label>
            <label style="margin: 10px;"></label>
            <router-link to="/summery_student"><li></li></router-link>
            <router-link to="/summery_tranfer"><li></li></router-link>
            <router-link to="/summery_plan"><li></li></router-link>
            
            <!-- <router-link to="/data_bot"><li></li></router-link> -->
          </ul>
        </div>
      </transition>
      <transition name="slide" style="text-align: left;">
        <div :class="['sidebar', { 'sidebar-active': isSidebarVisible }]" v-if="!user">
          <ul>
            <router-link to="/"><li></li></router-link>
          </ul>
        </div>
      </transition>
    </header>

    <!-- Content -->
    <main>
      <slot></slot>
    </main>

    <!-- Footer -->
    <footer>
      <p> 2024 </p>
    </footer>

    <!--  Chatbot -->
    <!-- <div class="chat-bot">
      <button @click="openChatBot"><i class="fa-solid fa-robot"></i> Chat Bot</button>
    </div> -->
  </div>
</template>

<script>

import { provide, computed } from "vue";
import { mapState, mapActions } from 'vuex';
import { useStore } from "vuex";

export default {
  setup() {
    const store = useStore(); //  Store 
    const user = computed(() => store.state.user); //  Vuex

    provide("user", user); //  user  Component 

    return { user };
  },
  name: "MainLayout",
  computed: {
    ...mapState({
      user: (state) => state.user, //  Vuex Store
      isSidebarVisible: (state) => state.isSidebarVisible, //  Sidebar  Vuex
    }),
    isAdmin() {
      return this.user && this.user.user_status === "0"; // Admin
    },
    isTeacher() {
      return this.user && this.user.user_status === "1"; // Teacher
    },
    isOfficer() {
      return this.user && this.user.user_status === "2"; // Officer
    },
    isStudent() {
      return this.user && this.user.user_status === "3"; // Student
    },
  },
  methods: {
    ...mapActions(["clearUserData", "toggleSidebar"]), //  action toggleSidebar
    logout() {
    if (confirm("")) {
      localStorage.removeItem("user_data"); //  localStorage
      this.$store.dispatch("clearUserData"); //  action clearUserData
      this.$router.push("/"); //  homepage
    }
  },

    getStatusLabel(status) {
      const statusMap = {
        "0": "",
        "1": "",
        "2": "",
        "3": "",
      };
      return statusMap[status] || "";
    },
  },
};
</script>

<style>
@import "@/assets/css/styles.css";
</style>