<template>
     <MainLayout>
       <div class="form-container">
          <h1></h1>
         <h2> : {{ user.std_id }} : {{ user.std_name }} {{ user.std_last }} </h2>
          <!-- Popup  -->
            <div v-if="change_popup" class="popup-overlay">
                  <div class="popup-content">
                  <h3> {{ sub_name }} </h3>
                  <div class="dropdown-container" style=" width: 600px;">
                       <select  v-model="select_year" id="year">
                           <option value="" disabled></option>     
                                   <option value="3"> 3</option>
                                   <option value="4"> 4</option>
                         </select>
                         
                         <select v-model="select_term" id="term">
                              <option value="" disabled></option>    
                              <template v-for="item in open_subject" :key="item.subject_id">
                                   <option v-if="item.subject_id === sub_id">{{ item.term }}</option>
                              </template>
                         </select>
                    </div>
                    <button class="btn-remove"
                    style="font-size: 18px; background-color: #00ff00; margin-right: 10px;"
                    @click="update_plan"></button>
                    <button class="btn-remove" style="font-size: 18px; background-color: #a3a3a3;" @click="closePopup"></button>
                  </div>
            </div>

         <!--  [, ] -->
               <div v-for="group in groupedPlan" :key="group.yearTerm" class="grouped-table">
               <h2> {{ group.term }}  {{ group.year === 4 ? Number(user.batch_year) + 1 : user.batch_year }}</h2>
                    <table class="data-stdplan-table">
                    <thead>
                         <tr>
                         <th></th>
                         <th></th>
                         <th></th>
                         <th></th>
                         <th v-if="!group"></th>
                         </tr>
                    </thead>
                    <tbody>
                         <template v-for="item in group.plans" :key="item.subject_id">
                         <tr :class="{'changed': item.hasChanged}">
                              <td>{{ item.subject_id }}</td>
                              <td style="text-align: left; width: 450px;" v-if="item.subject_name">{{ item.subject_name }}</td>
                              <td style="text-align: left; width: 450px;" v-else>{{ item.subject_nameTh }}</td>

                              <td v-if="item.theory_credits + item.practical_credits < 3">
                                   {{ item.practical_credits + item.theory_credits }} 
                                   ({{ item.theory_credits }}-{{ item.practical_credits * 2 }}-{{ 3 - (item.theory_credits + (item.practical_credits * 2)) }})
                              </td>
                              <td v-else-if="item.theory_credits + item.practical_credits <= 5">
                                   {{ item.practical_credits + item.theory_credits }} 
                                   ({{ item.theory_credits }}-{{ item.practical_credits * 2 }}-{{ 9 - (item.theory_credits + (item.practical_credits * 2)) }})
                              </td>
                              <td v-else>
                                   {{ item.practical_credits + item.theory_credits }} 
                                   ({{ item.theory_credits }}-{{ item.practical_credits }}-0)
                              </td>
                              <td>{{ item.group_id }}</td>
                              <td>
                                   <button class="btn-remove" 
                                        @click="openChangePopup(item.subject_name, item.subject_id)" 
                                        style="background-color: #00ccff; margin-right: 10px;">
                                        <i class="fa-solid fa-repeat"></i>
                                   </button>
                              </td>
                         </tr>
                         </template>
                         <!--  -->
                         <tr>
                              <td colspan="2" style="text-align: right;">:</td>
                              <td>{{ group.totalCredits }}</td>
                              <td colspan="2"></td>
                         </tr>
                    </tbody>
                    </table>
                    <br>

               </div>
          
         <h4 style="font-weight: lighter;"> ***  
          <br> (--)</h4>
          <h2>: {{ totalTransferCredits }} : {{ totalAllCredits }} : 125</h2>
          <div class="btn-group">
               <button class="btn-back" style="font-size: 20px; width: 200px;" @click="goBack">
                    <i class="fa-solid fa-arrow-left-long"> </i>
               </button>
               <button  class="btn-back" style="font-size: 20px; background-color:  #3385ff; width: 200px;" @click="resetChanges">
                    <i class="fa-solid fa-arrow-rotate-left"> </i>
               </button>               
               <button  class="btn-back" style="font-size: 20px; background-color:  #33cc33; width: 200px;" @click="saveChanges(user.std_id)">
                    <i class="fa-solid fa-floppy-disk"> </i>
               </button>
       </div>
     <h2></h2>
     <table class="data-stdplan-table" style="margin-bottom: 100px;">
          <thead>
          <tr>
               <th></th>
               <th></th>
               <th></th>
               <th></th>
               <th></th>
               <th></th>
          </tr>
          </thead>
          <tbody>
          <template v-for="(group, category) in credit_status" :key="category">
       
          <tr>
               <td colspan="7" style="text-align: left;"><strong>{{ category }}</strong></td>
          </tr>
          
          <template v-for="item in group" :key="item._id">
          <tr v-if="item.status == ''" style="color: #ff1a1a;">
               <td style="text-align: left; ">{{item.group_id +" "+ item.group_name }}</td>
               <td>{{ item.required_credits }}</td>
               <td>{{ item.tranfer_credits }}</td>
               <td>{{ item.need_credit }}</td>
               <td>{{ item.earned_credits }}</td>
               <td>{{ item.credit_isneed}}</td>

          </tr>
          <tr v-else style="color: #33cc33;">
               <td style="text-align: left;">{{ item.group_id +" "+ item.group_name }}</td>
               <td>{{ item.required_credits }}</td>
               <td>{{ item.tranfer_credits }}</td>
               <td>{{ item.need_credit }}</td>
               <td>{{ item.earned_credits }}</td>
               <td>{{ item.credit_isneed}}</td>
          </tr>
          </template>
        </template>
          </tbody>
     </table>
     </div>
     
     </MainLayout>
   </template>
   
<script>
import { inject, computed } from "vue";

import MainLayout from './main_layout.vue';
import axios from 'axios';

export default {
     setup() {
          const user = inject("user"); //  user  MainLayout.vue
          const userData = computed(() => user?.value || {}); //  undefined
          console.log("User Data:", userData.value); // 
          return { user: userData };
     },
     name: "std_changePlan_page",
     components: {
          MainLayout
     },
     computed: {

          calculateTotalCredits() {
               return (plans) => {
               return plans.reduce((sum, plan) => {
                    const totalCredits = plan.theory_credits + plan.practical_credits;
                    return sum + totalCredits;
               }, 0);
               };
          },
          totalAllCredits() {
               return this.groupedPlan.reduce((sum, group) => sum + group.totalCredits, 0);
          },
          filteredGroups() {
               return (term) => { if(term == ""){
                                        term = 3
                                   }
                    return this.group_list.filter(item => 
                         this.open_subject.some(openItem => openItem.group_id === item.group_id
                         && openItem.term == term)
                    );
               };
          },
          credit_status() {
                //  reduce()  `sort`
               return this.group_list.reduce((acc, item) => {
               if (!acc[item.sort]) {
                    acc[item.sort] = [];
               }
               acc[item.sort].push(item);
               return acc;
               }, {});
          },
          //  practical credits
          totalPracticalCredits() {
               return this.transfer_list.reduce(
                    (sum, item) => sum + Number(item.practical_credits || 0), 0
               );
          },
          //  theory credits
          totalTheoryCredits() {
               return this.transfer_list.reduce(
                    (sum, item) => sum + Number(item.theory_credits || 0), 0
               );
          },
          //  practical + theory credits 
          totalTransferCredits() {
               return this.totalPracticalCredits + this.totalTheoryCredits;
          },
     },
     data(){
          return{
               std_planlist: [],
               temp_planlist:[],
               term:"",
               groupedPlan: [],
               group_list:[],
               subject_list:[],
               plans_list:[],
               spacialTerm: false,
               open_subject:[],
               transfer_list:[],
               change_popup: false,
               sub_name: "",
               sub_id: "",
               select_term: "",
               select_year: "",

          };
     },
     methods:{
          //  Popup
          openChangePopup(sub_name,sub_id) {
               this.sub_name = sub_name;
               this.sub_id = sub_id;
               this.change_popup = true;
          },
               //  Popup
          closePopup() {
               this.change_popup = false;
          },
          async fetchOpen_subject(){ // 
          console.log(this.selectGroup)
          try{
               const response = await axios.post("http://127.0.0.1:3000/api/getopensubject",{
                         group_id:this.selectGroup
                    }); 
                    if(response.data.result == "1"){
                         this.open_subject = response.data.datalist;
                         this.groupPlans();
                         
                    }
                    else if(response.data.result == "2"){
                         this.open_subject = [];
                         this.groupPlans();
                         
                    }
          }
          catch(error){
               console.error("Error fetching subject:", error);
          }
          },

          async fetchTransfer_list(){ // 
               try{
                    const response = await axios.post("http://127.0.0.1:3000/api/getTranfer_plan",{
                         std_id: this.user.std_id
                    }); // URL  API
                    if(response.data.result == "1"){
                         // console.log("..--..",response.data.datalist);
                         this.transfer_list = response.data.datalist; //  transfer_list
                    }
                    else if(response.data.result == "0"){
                         this.transfer_list = [];
                    }

               }
               catch(error){
                    console.error("Error fetching subject:", error);  
               }
          },
          
          async fetchStudent_plan(){
               try{
                    const response = await axios.post("http://127.0.0.1:3000/api/getStudentplan_list", {
                         std_id: this.user.std_id
                    });
                    if (response.data.result === "1") {
                         // console.log(response.data.datalist);
                         this.std_planlist = response.data.datalist; //  state
                         this.temp_planlist = JSON.parse(JSON.stringify(this.std_planlist)); // clone 
                         this.groupPlans(); // 
                              
                    } else if(response.data.result === "0"){
                         this.std_planlist = [];
                         this.temp_planlist = []; // 
                         this.groupPlans();
                    }
                    
               }
               catch (error) {
                    console.error("Error fetching subjects:", error);
               }
          },
          async fetchStructure(){
               try {
                    const response = await axios.get("http://127.0.0.1:3000/api/getStructure",{
                    }); // URL  API
                    this.group_list = response.data.datalist; //  state
                    this.mergeCredits(); // 
               } catch (error) {
                    console.error("Error fetching subject:", error);
               }
          },
          async fetchSubject() {
               try {
                    if (!this.selectGroup) {
                         this.subject_list = []; // 
                         return;
                    }
                    
                    const response = await axios.post("http://127.0.0.1:3000/api/getSubject", {
                         group_id: this.selectGroup, //  selectGroup  API
                    });
                    
                    if (response.data.result == "1") {
                         this.subject_list = response.data.datalist; //  state
                    } else {
                         alert("");
                    }
                    } catch (error) {
                    console.error("Error fetching subject:", error);
               }
          },
          update_plan() {
                // 
               let filteredPlans = this.temp_planlist.filter(
                    plan => plan.year === Number(this.select_year) && plan.term === Number(this.select_term));
               
               // /
               console.log("Filtered Plans:", filteredPlans);

               //  Practical Credits  Theory Credits 
               let totalPcredits = filteredPlans.reduce((sum, plan) => sum + Number(plan.practical_credits || 0), 0);
               let totalTcredits = filteredPlans.reduce((sum, plan) => sum + Number(plan.theory_credits || 0), 0);

               let selectData = this.open_subject.find(item => item.subject_id === String(this.sub_id));
               if (!selectData) {
                    alert(" ");
                    return;
               }

               let pCredits = Number(selectData.practical_credits);
               let tCredits = Number(selectData.theory_credits);
               let totalCredits = totalPcredits + totalTcredits;
               let newPcredits = totalCredits + pCredits;
               let newTcredits = totalCredits + tCredits;

               console.log(`New Practical Credits: ${newPcredits}, New Theory Credits: ${newTcredits}`);

               if(confirm("")){
                    // 
                    if (newPcredits > 25 || newTcredits >25 ) {
                         alert("  25");
                         return;
                    }
                    if((newPcredits > 22|| newTcredits > 22) && this.spacialTerm == true){
                         alert("  25 ");
                         return;
                    }
                    if (newPcredits >= 22 || newTcredits > 22) {
                         if (!confirm(" 22 ?")) {
                              return;
                         }
                    }
                    //  temp_planlist
                    let existingPlan = this.temp_planlist.find(item => item.subject_id === this.sub_id);
                    if (existingPlan) {
                         //  year  term 
                         existingPlan.year = Number(this.select_year);
                         existingPlan.term = Number(this.select_term);
                         existingPlan.hasChanged = true; // 
                         console.log(" temp_planlist ");
                         this.change_popup = false;
                    } else {
                         alert("");
                    }

                    this.groupPlans(); // 
                    this.mergeCredits(); // 
                              }
          },

          resetChanges() {
               this.temp_planlist = JSON.parse(JSON.stringify(this.std_planlist)); // 
               this.groupPlans(); // 
               alert("!"); // 
          },
               // 
          async saveChanges(std_id) {
               if (confirm("?")) {
                    try {
                         //  temp_planlist  std_planlist  array 
                         let tempPlan = Array.isArray(this.temp_planlist) ? this.temp_planlist : [this.temp_planlist];
                         let originalPlan = Array.isArray(this.std_planlist) ? this.std_planlist : [this.std_planlist];

                         // 
                         let changedPlans = tempPlan.map(tempItem => {
                              let originalItem = originalPlan.find(
                                   item => item.subject_id === tempItem.subject_id &&
                                   item.term === tempItem.term &&
                                   item.year === tempItem.year &&
                                   item.group_id === tempItem.group_id
                              );
                              if (!originalItem) return { ...tempItem }; //  

                              let changedFields = {};
                              Object.keys(tempItem).forEach(key => {
                                   if (tempItem[key] !== originalItem[key]) {
                                   changedFields[key] = tempItem[key]; // 
                                   }
                              });

                              return Object.keys(changedFields).length > 0
                                   ? { plan_id: tempItem.plan_id, ...changedFields } 
                                   : null;
                         }).filter(item => item !== null); // 
                         //   API
                         if (changedPlans.length === 0) {
                              alert("");
                              return;
                         }
                         else{
                              console.log(":", changedPlans);
                              //  API
                              const response = await axios.post("http://127.0.0.1:3000/api/setAdjustplan_list", {
                                   std_id: std_id,
                                   plans: changedPlans,  
                              });
     
                              if (response.data.result === "1") {
                                   alert("!");
                                   //  std_planlist 
                                   this.std_planlist = JSON.parse(JSON.stringify(this.temp_planlist));
                                   this.$router.push("/");
                              }
                              else if(response.data.result === "2"){
                                   alert("");
                              }
                              else {
                                   alert("");
                              }
                         }
                    } catch (error) {
                         console.error("Error saving changes:", error);
                    }
               }
          },
          groupPlans() {
               // 
               const yearTermPairs = [
               [3, 1],
               [3, 2],
               [3, 3],
               [4, 1],
               [4, 2],
               ];
               //  [, ]
               this.groupedPlan = yearTermPairs.map(([year, term]) => {
                    const plans = this.temp_planlist.filter(plan => plan.year === year && plan.term === term);
                    const totalCredits = plans.reduce((sum, plan) => {
                         if((sum + (plan.theory_credits + plan.practical_credits)) > 22){
                              this.spacialTerm = true;
                         }
                         
                         return sum + (plan.theory_credits + plan.practical_credits);
                    }, 0);
               return {
                    yearTerm: `${year}-${term}`, //  key 
                    year,
                    term: term === 3 ? "" : term, //  3
                    plans: this.temp_planlist.filter(plan => plan.year === year && plan.term === term),
                    totalCredits,
                    //  open_subject  open
                    open: this.open_subject.filter(plan => plan.term === term), 
               };
               });
          },
          mergeCredits() {
               this.group_list = this.group_list.map(group => {
                    // console.log(" group_list:", group.group_id);

                    //  group_id
                    let plans = this.temp_planlist.filter(plan => String(plan.group_id) === String(group.group_id));
                    let tranfers_plan = this.transfer_list.filter(plan => String(plan.group_id) === String(group.group_id));
                    // console.log(" tranfers_plan:", tranfers_plan);

                    // console.log(" temp_planlist:", plans);

                    // 
                    let totalCredits_need = plans.reduce((sum, plan) => sum + (plan.theory_credits + plan.practical_credits), 0);
                    let totalCredits_tranfer = tranfers_plan.reduce((sum, plan) => sum + (plan.theory_credits + plan.practical_credits), 0);
                    let totalCredits_all = totalCredits_need + totalCredits_tranfer;

                    let credit_need = group.credit - totalCredits_all;
                    if (credit_need < 0) {
                         credit_need = 0;
                    }
                    // 
                    let status = totalCredits_all >= group.credit ? "" : "";

                    return {
                         ...group, //  group
                         _id: Date.now().toString(), //  ID 
                         required_credits: group.credit, // 
                         tranfer_credits: totalCredits_tranfer, // 
                         need_credit: totalCredits_need, // 
                         earned_credits: totalCredits_all, // 
                         credit_isneed: credit_need, // 
                         status: status // 
                    };
               });

               // console.log(" group_list:", this.group_list);
          },

          goBack() {
          // 
               this.$router.go(-1)
          },
     },
          watch: {
          selectGroup(newValue) {
               console.log("Group ID changed:", newValue);
               this.selectGroup_id = newValue;
               this.fetchOpen_subject(); //  fetchOpeb_subject  selectGroup 
          },
          selectPlan(newValue){
               this.selectPlan = newValue;
               this.fetchStandardPlan_list();
          },
          temp_planlist(newValue) {
               console.log("temp_planlist changed:", newValue);
               this.mergeCredits(); // 
          },
     },
     async mounted() {
          await this.fetchStudent_plan();
          await this.fetchTransfer_list();
          await this.fetchSubject();
          await this.fetchOpen_subject();
          this.fetchStructure();
     }
}
</script>