<template>
  <MainLayout>
  <div class="login-container">
    <div class="login-box">
      <h2></h2>
      <form @submit.prevent="handleLogin">
        <div class="form-group">
          <label for="username"></label>
          <input
            type="text"
            id="username"
            v-model="username"
            placeholder=""
            required
          />
        </div>
        <div class="form-group">
          <label for="password"></label>
          <input
            type="password"
            id="password"
            v-model="password"
            placeholder=""
            required
          />
        </div>
        <button type="submit" class="login-button"></button>

      </form>
      <p v-if="message" style="color: red;">{{ message }}</p>
    </div>
  </div>
  </MainLayout>
</template>

<script>
import axios from 'axios';
import MainLayout from './main_layout.vue';
import { mapActions } from 'vuex'; //  mapActions  action  store

export default {
  name: 'LoginPage',
  components: {
    MainLayout  
  },
  data() {
    return {
      username: '',
      password: '',
      message: ''
    };
  },
  created() {
    //  localStorage  ()  Vuex
    const storedUser = localStorage.getItem('user_data');
    if (storedUser) {
      this.setUserData(JSON.parse(storedUser)); //  Vuex store
    }
  },
  methods: {
    ...mapActions(['setUserData']),
    async handleLogin() {
      try {
        const response = await axios.post('http://127.0.0.1:3000/api/getlogin', {
          username: this.username,
          password: this.password
        });
        console.log(response.data); //  API
        if (response.data.result == "1"){
          this.message = response.data.message;
          //  login   Vuex store
          this.setUserData(response.data.datalist);
          //  Local Storage
          localStorage.setItem("user_data", JSON.stringify(response.data.datalist));
          //  Home
          this.$router.push("/");
        }
        else{
          this.message = response.data.message;
        }
      } catch (error) {
        if (error.response) {
          this.message = error.response.data.message;
        } else {
          this.message = 'Error connecting to server.';
        }
      }
    }
  }
};
</script>
